import Layout from '@layouts/main';

export default [
  {
    path: '/mainPages',
    component: Layout,
    meta: {
      title: '工作台',
      authority: 'mainPages',
      icon: 'support'
    },
    order: 6,
    redirect: '/mainPages/',
    children: [
      {
        path: 'myDesk',
        name: 'myDesk',
        meta: {
          title: '我的工作台',
          icon: 'dataview',
          authority: 'myDesk'
        },
        component: () => import('@views/mainPages/myDesk/index.vue')
      },
      {
        path: 'manageDetails',
        name: 'manageDetails',
        component: () => import('@views/mainPages/myDesk/manageDetails.vue'),
        meta: {
          title: '办理详情',
          icon: 'role',
          authority: 'manageDetails'
        }
      }
    ]
  }
];
