import routesArrary from '@router/routes';

function hasOneRootChild(route) {
  if (!route.children) return false;
  const showChildren = route.children.filter((item) => !item.meta.hidden);
  return showChildren.length === 1 && showChildren[0].path === 'index';
}

function routesToMenus(routes, parentPath) {
  const menus = [];
  routes.forEach((item, index) => {
    // console.log(item);
    if (item.meta.hidden) return;
    if (hasOneRootChild(item)) {
      const rootChild = item.children[0];
      const menu = {
        id: index,
        code: rootChild.path ? item.path + '/' + rootChild.path : item.path,
        title: rootChild.meta.title,
        order: item.order,
        icon: rootChild.meta.icon
      };
      menus.push(menu);
    } else {
      const menu = {};
      const path = parentPath ? parentPath + '/' + item.path : item.path;
      const title = item.meta ? item.meta.title : item.path;
      const icon = item.meta.icon ? item.meta.icon : '';
      if (item.children) {
        menu.children = routesToMenus(item.children, path);
      }
      menu.id = path;
      menu.code = path;
      menu.title = title;
      item.order && (menu.order = item.order);
      menu.icon = icon;
      menus.push(menu);
    }
  });
  return menus;
}

const menuData = routesToMenus(routesArrary, '').sort(function(a, b) {
  return (a.order || 1) - (b.order || (a.order || 1) + 1);
});
const routes = {
  namespaced: true,
  state: {
    menuData: menuData
  },
  mutations: {},
  actions: {}
};

export default routes;
