













































































































// import jq from 'jquery';
import { Prop, Component, Vue } from 'vue-property-decorator';
import {
  resetMarket,
  saveMarket
} from '../../api/multidimensionalMonitoring.js';

@Component
export default class NoteCard extends Vue {
  @Prop({ default: false }) readonly noTitle: boolean;
  @Prop(String) readonly title: string;
  @Prop(String) readonly subTitle: string;
  @Prop(String) readonly postNature: string;
  @Prop({ default: [] }) readonly actions: Array<any>;
  @Prop(Boolean) readonly shadow: boolean;
  @Prop(Boolean) readonly changeSelect: false;
  @Prop(Boolean) readonly titleBgColor: boolean;
  @Prop({ default: [] }) marketList: Array<any>;
  @Prop({ default: [] }) defaultChecked: Array<any>;
  @Prop(Boolean) readonly hasUpload: false;
  private checkList: Array<any> = [];
  private fileList: Array<any> = [];
  private importFiles: Array<any> = [];
  private numAdd = 1;
  changeNodes(val) {
    this.checkList = val;
    this.$emit('changeNodes', val);
  }

  clientCheckAll() {
    resetMarket().then(({ error }) => {
      if (!error) {
        const aaa: any = this.$parent;
        aaa.getMarket();
        this.$message.success('重置成功');
        this.$emit('callBackInfo', this.numAdd++);
      } else {
        this.$message.error('重置失败');
      }
    });
  }
  clientCanCelCheckAll() {
    const param = {
      markets: this.defaultChecked
    };

    saveMarket(param).then(({ error }) => {
      if (!error) {
        const aaa: any = this.$parent;
        const selectMenu: any = this.$refs.selectHidden;
        aaa.getMarket();
        this.$message.success('保存成功');
        this.$emit('callBackInfo', this.numAdd++);
        selectMenu.toggleMenu();
      } else {
        this.$message.error('保存失败');
      }
    });
  }
  handleUploadFile(file, fileList) {
    this.importFiles = fileList;
    this.$emit('fileChange', this.importFiles);
  }
}
