import ajax from '@utils/ajax';

// const serverPre = ajax.serverUrlMock;
const serverPre = ajax.serverUrl;
const baseServerName = '/billow-diplomatic';
const serverName = '/billow-diplomatic/process-temp';
const manualServerName = '/billow-diplomatic/handle-mode-hand';
const timingServerName = '/billow-diplomatic/handle-mode-timing';
const externalServerName = '/billow-diplomatic/handle-mode-external';
const tranceServer = '/yss-base-billows';
const ruleServer = '/rule-engine';

// 流程模板修改时，发起方式单独调各自接口
// 手工发起 新增
export async function manualAdd(params) {
  return ajax.post(serverPre(`${manualServerName}/save`), params);
}

// 手工发起 修改
export async function manualUpdate(params) {
  return ajax.post(serverPre(`${manualServerName}/update`), params);
}

// 手工发起 删除
export async function manualDelete(id) {
  return ajax.post(serverPre(`${manualServerName}/delete/${id}`));
}

// 定时发起 新增
export async function timingAdd(params) {
  return ajax.post(serverPre(`${timingServerName}/save`), params);
}

// 定时发起 修改
export async function timingUpdate(params) {
  return ajax.post(serverPre(`${timingServerName}/update`), params);
}

// 定时发起 删除
export async function timingDelete(id) {
  return ajax.post(serverPre(`${timingServerName}/delete/${id}`));
}

// 指令发起 新增
export async function externalAdd(params) {
  return ajax.post(serverPre(`${externalServerName}/save`), params);
}

// 指令发起 修改
export async function externalUpdate(params) {
  return ajax.post(serverPre(`${externalServerName}/update`), params);
}

// 指令发起 删除
export async function externalDelete(id) {
  return ajax.post(serverPre(`${externalServerName}/delete/${id}`));
}

// 流程模板导出
export async function tempExport(id) {
  ajax.download(serverPre(`${serverName}/export?tempId=${id}`));
}

// 流程模板导入
export async function tempUpload(file) {
  return ajax.post(serverPre(`${serverName}/importFile`), file);
}
// 流程配置管理列表查询
export async function processQuery(params) {
  return ajax.get(serverPre(`${serverName}/list`), params);
}
// 流程配置管理 新增
export async function processAdd(params) {
  return ajax.post(serverPre(`${serverName}/add`), params);
}
// 流程配置管理 修改
export async function processEdit(params) {
  return ajax.post(serverPre(`${serverName}/edit`), params);
}

// 流程配置管理 回显--常规属性等后三个tab的详情
export async function processDetail(params) {
  return ajax.post(serverPre(`${serverName}/view`), params);
}

// 流程模板发布
export async function tempPublish(params) {
  return ajax.post(serverPre(`${serverName}/publish`), params);
}

// 流程模板删除
export async function tempDelete(params) {
  return ajax.post(serverPre(`${serverName}/delete`), params);
}

// 流程模板预览、修改时获取详情
export async function tempDetail(params) {
  return ajax.get(serverPre(`${serverName}/preview-chart`), params);
}

// 流程模板预览 最新版流程图
export async function tempPublishedDetail(params) {
  return ajax.get(serverPre(`${serverName}/preview-publishchart`), params);
}

// 流程模板复制
export async function tempCopy(params) {
  return ajax.get(serverPre(`${serverName}/copy`), params);
}

// 流程模板校验
export async function tempValidate(params) {
  return ajax.post(serverPre(`${serverName}/validate`), params);
}

// 保存流程图
export async function saveCanvas(params) {
  return ajax.post(serverPre(`${serverName}/save-xml`), params);
}

//  规则决策保存
export async function saveDecisionRule(params) {
  return ajax.post(
    serverPre(`${baseServerName}/decision-rule/save-decision-rule`),
    params
  );
}
// 读取流程实例变量列表
export async function getFlowInstanceVariables() {
  return ajax.get(serverPre(`${tranceServer}/expression/list`));
}

// 岗位列表
export async function stationQuery() {
  return ajax.get(serverPre(`${tranceServer}/group/list`));
}

// 岗位下用户列表
export async function userQueryByGroup(id) {
  return ajax.get(serverPre(`${tranceServer}/user/group-id/${id}`));
}

// 岗位下用户列表(多选)
export async function userQueryByGroups(ids) {
  return ajax.post(serverPre(`${tranceServer}/user/group-ids/user`), ids);
}

// 查询规则引擎通用函数
export async function getCommonRuleFuncs() {
  return ajax.get(serverPre(`${ruleServer}/ruleMethodHandler/getMethodList`));
}

// 推送消息标记已读状态
export async function markRead(id) {
  return ajax.get(serverPre(`/mail_info/updateAlreadyRead/${id}`));
}
// 获取模板表单列表
export async function listFormProcess(params) {
  return ajax.get(serverPre(`${baseServerName}/process-form/listForm`), params);
}
// 修改模板表单列表
export async function editFormProcess(params) {
  return ajax.post(
    serverPre(`${baseServerName}/process-form/editForm`),
    params
  );
}
