import Layout from '@layouts/main';

export default [
  {
    path: '/home',
    component: Layout,
    meta: {
      authority: 'home',
      title: '首页',
      icon: 'stack-line'
    },
    order: 1,
    redirect: '/home/index',
    children: [
      {
        path: 'index',
        name: 'Home',
        meta: {
          title: '首页',
          authority: 'index',
          // icon: 'function-line',
          ignoreAuth: true
          // hidden:true,
        },
        component: () => import('@views/home')
      }
    ]
  }
];
