const rowHttp = {
  type: 'object',
  properties: {
    status: {
      type: 'number',
      description: '状态'
    },
    message: {
      type: 'string',
      description: '描述'
    },
    data: {
      type: 'object',
      properties: {
        total: {
          type: 'number',
          description: '数据量'
        },
        rows: {
          type: 'array',
          items: {
            type: 'object',
            properties: {
              code: {
                type: 'string'
              },
              name: {
                type: 'string'
              },
              proType: {
                type: 'string'
              },
              number: {
                type: 'number'
              },
              amount: {
                type: 'number'
              },
              date: {
                type: 'date'
              },
              frequency: {
                type: 'string'
              },
              limit: {
                type: 'string'
              }
            }
          },
          description: '数据，都是key、value形式的'
        }
      }
    }
  }
};

export default {
  'get|/amc-datalink-server/data-view/list': (options) => {
    const dataHandle = [];
    dataViewFields.forEach((items) => {
      if (items.type === Number(getUrlParams(options.url).type)) {
        dataHandle.push(items);
      }
    });
    return {
      status: 200,
      data: {
        rows: dataHandle,
        total: dataHandle.length
      }
    };
  },
  'post|/amc-datalink-server/data-view/test/data-base': (options) => {
    return {
      status: 200,
      data: {
        total: 10,
        rows: dataViewsList['1']
      }
    };
  },
  'post|/amc-datalink-server/data-view/test/http': (options) => {
    return {
      status: 200,
      data: {
        result:
          '',
        jsonSchema: rowHttp,
        total: 10
      }
    };
  },
  'post|/amc-datalink-server/link/add': (options) => {
    const link = JSON.parse(options.body);
    link.id = `link_${linkList.length + 1}`;
    linkList.push(link);
    return {
      status: 200,
      message: 'success',
      data: {
        linkId: link.id
      }
    };
  },
  'get|/amc-datalink-server/link/list': (options) => {
    const params = getUrlParams(options.url);

    return {
      status: 200,
      message: 'success',
      data: {
        total: 3,
        rows: linkList.filter(
          (item) => item.type === Number.parseInt(params.type)
        )
      }
    };
  },
  'get|/amc-datalink-server/link/detail': (options) => {
    const params = getUrlParams(options.url);
    return {
      status: 200,
      message: 'success',
      data: arrayFind(linkList, 'id', params.id)
    };
  },
  'post|/amc-datalink-server/data-view/add': (options) => {
    const dataView = JSON.parse(options.body);
    dataViewFields.push({
      ...dataView,
      id: `datav_${dataViewFields.length + 1}`,
      status: 1
    });
    return {
      status: 200,
      message: 'success',
      data: `datav_${dataViewFields.length}`
    };
  },
  'post|/amc-datalink-server/data-view/edit': (options) => {
    const dataView = JSON.parse(options.body);
    dataViewFields = dataViewFields.map((item) => {
      if (item.id === dataView.id) {
        return dataView;
      } else {
        return item;
      }
    });

    return {
      status: 200,
      message: 'success'
    };
  },
  'post|/amc-datalink-server/data-view/updStatus': (options) => {
    return {
      status: 200,
      message: 'success'
    };
  },
  'get|/amc-datalink-server/data-view/detail': (options) => {
    const params = getUrlParams(options.url);

    return {
      status: 200,
      message: 'success',
      data: dataViewFields.filter((item) => item.id === params.id)[0]
    };
  },
  'get|/amc-datalink-server/data-view/fields': (options) => {
    const params = getUrlParams(options.url);
    const dictFlag = params.dictFlag;
    const status = params.status;
    const ids = params.ids;
    return {
      status: 200,
      message: 'success',
      data: dataViewFields.filter((item) => {
        if (dictFlag) {
          return item.dictFlag === Number.parseInt(dictFlag);
        }
        if (status) {
          return item.status === Number.parseInt(status);
        }
        if (ids) {
          return item.id === ids;
        }
        return true;
      })
    };
  },
  'post|/amc-datalink-server/data-link/process/internal-query': (options) => {
    const params = JSON.parse(options.body);
    console.log(params);
    return {
      status: 200,
      message: 'success',
      data: {
        total: 100,
        rows: dataViewsList[params.viewId]
      }
    };
  },
  'post|/amc-datalink-server/data-link/process/data-push': (options) => {
    const params = JSON.parse(options.body);

    return {
      status: 200,
      message: 'success',
      data: {
        total: 100,
        rows: dataViewsList[params.viewId]
      }
    };
  },
  'post|/billow-diplomatic/todo-task/express-parse': () => {
    return {
      status: 200,
      message: 'success',
      data: dataViewsList['datav_1']
    };
  }
};
