<template>
  <div>
    <div class="collapsecon" @click="changeFoldingState">
      <i v-show="!foldingState" class="el-icon-s-fold"></i>
      <i v-show="foldingState" class="el-icon-s-unfold"></i>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';
export default {
  data() {
    return {};
  },
  methods: {
    ...mapMutations({ changeFoldingState: 'collapse/changeFoldingState' })
  },
  computed: {
    ...mapState({
      foldingState: (state) => state.collapse.foldingState
    })
  }
};
</script>
<style lang="scss">
.collapsecon {
  color: $color-icon;
  float: left;
  cursor: pointer;
  &:hover {
    color: $color-main;
    // background-color: $color-main;
    // transition: background-color .3s;
  }
}
</style>
