




























import { Vue, Component, Prop, Emit } from 'vue-property-decorator';
@Component
export default class SimpleDialog extends Vue {
  @Prop(String) dialogTitle: string;
  @Prop({ default: false }) btnLoading: boolean;
  @Prop({ default: false }) dialogVisible: boolean;
  @Prop({ default: false }) bottonShow: boolean;
  @Prop({ default: false }) sureDisabled: boolean;
  @Prop({ default: false }) appendToBody: boolean;
  @Prop({ default: '保存' }) saveText: string;
  @Prop({ default: '取消' }) cancelText: string;
  @Prop(String) dialogWidth: string;
  @Emit('saveEmit')
  saveDialog() {}
  @Emit('cancelEmit')
  cancelDialog() {}
  @Emit('OpenedEmit')
  handleOpened() {}
}
