<template>
  <el-tooltip
    effect="dark"
    :content="tab.name"
    placement="top-start"
    :disabled="isDisabled"
    slot="label"
  >
    <span class="tab-name" ref="tabName">
      {{ tab.name }}
    </span>
  </el-tooltip>
</template>

<script>
export default {
  props: {
    tab: {
      type: Object,
      required: true
    }
  },
  data: function() {
    return {
      isDisabled: true
    };
  },
  watch: {
    tab: {
      handler: function(val, oldVal) {
        if (val !== oldVal) {
          this.$nextTick(() => {
            setTimeout(() => {
              if (this.$refs.tabName.clientWidth) {
                this.isDisabled =
                  this.$refs.tabName.getBoundingClientRect().width >=
                  this.getTextWidth(val.name);
              }
            }, 0);
          });
        }
      },
      immediate: true
    }
  },
  // computed: {
  //   isDisabled() {
  //     this.$nextTick(() => {
  //       setTimeout(() => {
  //         return (
  //           this.$refs.tabName.clientWidth + 20 >=
  //           this.getTextWidth(this.tab.name)
  //         ); // 加了一个padding值
  //       }, 0);
  //     });
  //     return true;
  //   }
  // },
  methods: {
    getTextWidth(str) {
      let width = 0;
      const html = document.createElement('span');
      html.innerText = str;
      html.style.fontSize = '14px';
      html.className = 'getTextWidth';
      document.querySelector('body').appendChild(html);
      width = document.querySelector('.getTextWidth').getBoundingClientRect()
        .width;
      document.querySelector('.getTextWidth').remove();
      return width;
    }
  }
};
</script>
