<template>
  <div class="layout">
    <el-container class="page">
      <el-container>
        <el-aside :width="colWidth" class="el_asidet">
          <div class="logo">
            <img class="them_logo" src="@assets/images/logo_pic.png" alt="" />
            <!-- <img class="them" v-show="!foldingState" src="@assets/images/logo_title.png" alt=""> -->
            <div v-show="!foldingState" class="them">
              <div class="logo_con_text_chs">运营管理平台</div>
              <div class="logo_con_text_eng"
                >Operations Management Platform</div
              >
            </div>
          </div>
          <div
            style="height: calc(100% - 78px);overflow: auto;"
            class="side-bar"
          >
            <NavBar></NavBar>
          </div>
        </el-aside>
        <el-container>
          <el-header class="header" height="46px">
            <Header class="fr" />
          </el-header>
          <el-main style="height: calc(100vh - 47px);padding:0;">
            <TagViews style="width:100%"></TagViews>
            <Breadcrumb />

            <el-container class="conentWrap">
              <Content />
            </el-container>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from './Header';
import NavBar from './components/NavBar';
import TagViews from '@components/TagsView';
import Breadcrumb from '@components/Breadcrumb';
import Content from './Content';
import { mapState } from 'vuex';
import SocketClient from '@utils/socketClient';

export default {
  components: {
    Header,
    TagViews,
    Breadcrumb,
    Content,
    NavBar
  },
  data() {
    return {
      socket: '',
      stompClient: null,
      colWidth: '200px',
      notifyPromise: Promise.resolve()
    };
  },
  computed: {
    ...mapState({
      usercode: (state) => state.user.userInfo.usercode,
      foldingState: (state) => state.collapse.foldingState
    })
  },
  watch: {
    foldingState(val) {
      if (val === true) {
        this.colWidth = '65px';
      } else {
        this.colWidth = '200px';
      }
    }
  },
  mounted() {
    if (process.env.NODE_ENV === 'production') {
      this.connectWebSocket();
    }
  },
  destroyed() {
    if (process.env.NODE_ENV === 'production') {
      if (this.socket) {
        try {
          this.socket.close();
        } catch (error) {
          console.info(error);
        }
      }
    }
  },
  methods: {
    connectWebSocket() {
      try {
        this.socket = new SocketClient('/api/amc-message-center/socketServer');
        this.socket.init().then(() => {
          this.socket.getMessage(`/user/${this.usercode}/message`, (msgs) => {
            msgs.forEach((msg) => {
              this.notifyPromise = this.notifyPromise
                .then(this.$nextTick)
                .then(() => {
                  this.$notify.warning({
                    title: '通知',
                    dangerouslyUseHTMLString: true,
                    message: `<p>${msg.message}</p>`,
                    duration: 0,
                    onClose: () => {
                      this.socket.sendMessage('/aloneRequest', msg.messageId);
                    }
                  });
                });
            });
          });
        });
      } catch (error) {
        console.info(error);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.el-aside .side-bar {
  overflow: auto;
}
/*滚动条整体样式*/
.el-aside .side-bar::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}
.el-aside .side-bar::-webkit-scrollbar-thumb {
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 5px rgb(22, 42, 90);
  background: rgb(22, 42, 90);
}
/*滚动条里面轨道*/
.el-aside .side-bar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgb(22, 42, 90);
  border-radius: 1px;
  background: rgb(22, 42, 90);
}
.layout {
  height: 100vh;
}
.page {
  height: 100%;
}

.header {
  // background: $color-back-card;
  transition: background-color 0.8s;
  border-bottom: 1px solid $color-table-cutting;
}
.conentWrap {
  height: calc(100% - 65px);
  background-color: $color-back-page;
  transition: background-color 0.8s;
}
.el_asidet {
  height: 100%;
  background: $color-back-nav;
  transition: background-color 0.8s;
  overflow: hidden;
}
.logo {
  height: 78px;
  display: flex;
  justify-content: center;
  align-items: center;
  .them {
    // font-size: 18px;
    color: #fff;
    padding: 10px 0;
    width: 130px;
    .logo_con_text_chs {
      font: bold 18px 'Microsoft YaHei';
      width: 100%;
      white-space: nowrap;
      text-align: center;
    }
    .logo_con_text_eng {
      font: 12px 'Microsoft YaHei';
      white-space: nowrap;
      transform: scale(0.6) translate(-34px, 2px);
      width: 100%;
    }
  }
  .them_logo {
    max-width: 36px;
    height: auto;
  }
}
</style>
