export function authActionRender(opers) {
  const length = opers.length;
  return function(h, { record, $index }) {
    return h(
      'div',
      {},
      opers.map((item, index) => {
        if (!item.hidden || !item.hidden(record, $index)) {
          const btnProps = {
            type: 'text',
            size: 'small'
          };
          if (item.disabled) {
            btnProps.disabled =
              item.disabled instanceof Function
                ? item.disabled(record, $index)
                : item.disabled;
          }

          const btn = h(
            'authorize',
            {
              props: {
                code: item.code,
                key: `${item.code}_&${index}`
              }
            },
            [
              h(
                'el-button',
                {
                  props: btnProps,
                  on: {
                    click: () => item.onClick(record, $index)
                  }
                },
                item.text instanceof Function ? item.text(record) : item.text
              )
            ]
          );
          if (index + 1 > 0 && index + 1 < length) {
            const span = h(
              'span',
              {
                style: {
                  padding: '0 4px'
                }
              },
              '|'
            );
            return [btn, span];
          } else {
            return btn;
          }
        }
      })
    );
  };
}

export function actionRender(opers) {
  const length = opers.length;
  return function(h, { record, $index }) {
    return h(
      'div',
      {},
      opers.map((item, index) => {
        if (!item.hidden || !item.hidden(record)) {
          const btn = h(
            'el-button',
            {
              props: {
                type: 'text',
                size: 'small'
              },
              on: {
                click: () => item.onClick(record, $index)
              }
            },
            item.text
          );
          const span = h(
            'span',
            {
              style: {
                padding: '0 4px'
              }
            },
            '|'
          );
          if (index + 1 > 0 && index + 1 < length) {
            return [btn, span];
          } else {
            return btn;
          }
        }
      })
    );
  };
}
