import Layout from '@layouts/main';

export default [
  {
    path: '/reptileSetting',
    component: Layout,
    //   添加hidden属性，在菜单中隐藏该模块，注释hidden即可显示
    // hidden:true,
    meta: {
      title: '爬虫设置',
      authority: 'reptileSetting',
      icon: 'auth'
    },
    order: 4,
    redirect: '/reptileSetting/',
    children: [
      {
        path: 'spiderList',
        name: 'spiderList',
        meta: {
          title: '爬虫列表',
          icon: 'database',
          authority: 'spiderList'
        },
        component: () => import('@views/reptileSetting/spiderList.vue')
      },
      {
        path: 'variables',
        name: 'variables',
        meta: {
          title: '全局变量',
          icon: 'template',
          authority: 'variables'
        },
        component: () => import('@views/reptileSetting/variables.vue')
      },
      {
        path: 'functions',
        name: 'functions',
        meta: {
          title: '自定义函数',
          icon: 'template',
          authority: 'functions'
        },
        component: () => import('@views/reptileSetting/functions.vue')
      },
      {
        path: 'datasources',
        name: 'datasources',
        meta: {
          title: '数据源管理',
          icon: 'template',
          authority: 'datasources'
        },
        component: () => import('@views/reptileSetting/datasources.vue')
      }
    ]
  }
];
