import axios from 'axios';
import { getSessionStorage } from './storage';

axios.defaults.headers = {
  'X-Requested-With': 'XMLHttpRequest'
};
axios.defaults.timeout = 1000 * 60 * 10;

function requestHandle(params) {
  return new Promise((resolve, reject) => {
    axios(params)
      .then((res) => {
        if (res.data) {
          if (res.data.status === 200) {
            resolve({
              data: res.data.data ? res.data.data : null
            });
          } else if (res.data.status === 20122) {
            window.alert('无操作权限');
          } else {
            if (res.data.status === 20121) {
              try {
                document.location.replace('/login');
              } catch (err) {
                console.log(err);
              }
            } else {
              resolve({
                error: {
                  code: res.data.status,
                  message: res.data.message
                }
              });
            }
          }
        } else {
          reject(new Error('没有返回数据'));
        }
      })
      .catch((err) => {
        resolve({
          error: {
            code: 500,
            message: `${params.url}: ${err.message}`
          }
        });
      });
  });
}

function actionDownload(href, filename) {
  const aLink = document.createElement('a');
  aLink.href = href;
  aLink.style.display = 'none';
  if (filename) {
    aLink.setAttribute('download', filename);
  } else {
    aLink.download = '';
  }

  let event;
  if (window.MouseEvent) {
    event = new MouseEvent('click');
  } else {
    event = document.createEvent('MouseEvent');
    event.initMouseEvent(
      'click',
      true,
      false,
      window,
      0,
      0,
      0,
      0,
      0,
      false,
      false,
      false,
      false,
      0,
      null
    );
  }
  aLink.dispatchEvent(event);
  URL.revokeObjectURL(aLink.href);
}

function queryString(url, query) {
  const str = [];
  for (const key in query) {
    if (query[key]) {
      str.push(key + '=' + query[key]);
    }
  }
  const paramStr = str.join('&');
  return paramStr
    ? `${url}${url.indexOf('?') > -1 ? '&' : '?'}${paramStr}`
    : url;
}

function getHeaders(options) {
  const headers = Object.assign(
    {
      'Content-Type': 'application/json'
    },
    options
  );

  const token = getSessionStorage('auth_token');
  if (token) {
    headers.Authorization = token;
  }
  return headers;
}

function directDownload(url, param) {
  const defaultParam = {};

  // 添加token权限验证
  const token = getSessionStorage('auth_token');
  if (token) {
    defaultParam.token = token;
  }

  Object.assign(defaultParam, param);

  // 手工转换encodeURI不能识别的字符
  let eurl = encodeURI(queryString(url, defaultParam));
  eurl = eurl.replace(/\+/g, '%2B');
  console.log(eurl);
  actionDownload(eurl);
}

function loadConfig(url, params, op, type) {
  let config = {
    method: 'get',
    url: queryString(url, params),
    headers: getHeaders(op)
  };
  if (type === 'post') {
    config = {
      method: 'post',
      url: url,
      data: params,
      headers: getHeaders(op)
    };
  }
  return config;
}

function resDownload(url, params, op, filename, type = 'get') {
  return new Promise((resolve, reject) => {
    axios({ ...loadConfig(url, params, op, type), responseType: 'blob' }).then(
      (res) => {
        if (res.data) {
          if (filename === 'auto') {
            if (res.headers.filename) {
              filename = decodeURIComponent(res.headers.filename);
            }
          }
          actionDownload(
            window.URL.createObjectURL(new Blob([res.data])),
            filename
          );
          resolve({});
        } else {
          resolve({
            error: {
              code: 500,
              message: '文件下载失败'
            }
          });
        }
      }
    );
  });
}

export default {
  post: function(url, params, op) {
    return requestHandle(loadConfig(url, params, op, 'post'));
  },
  get: function(url, params, op) {
    return requestHandle(loadConfig(url, params, op, 'get'));
  },
  download: function(url, params, filename, type, op) {
    if (filename) {
      return resDownload(url, params, op, filename, type);
    } else {
      directDownload(url, params);
    }
  },
  serverUrl(apiName) {
    return `/api${apiName}`;
    // return apiName;
  },
  serverUrlMock(apiName) {
    return apiName;
  },
  queryString
};
