const dataCache = {
  state: {
    params: {}
  },
  mutations: {
    SET_CACHE_PARAMS: (state, payload) => {
      if (payload.params) {
        state.params[payload.moduleName] = payload.params;
      } else {
        delete state.params[payload.moduleName];
      }
    },
    // 删除所有缓存数据
    RESET_CACHE_PARAMS: (state) => {
      const keys = Object.keys(state.params);
      keys.length &&
        keys.forEach((key) => {
          key && delete state.params[key];
        });
    }
  }
};

export default dataCache;
