import { addSceneTask } from '@api/businessScenarioManage.js';
import { addTask, editTask } from '@api/taskLibraryManage.js';
import { arrayFind } from '@utils/util';
import { dataViewFields } from '@api/viewData';
import { Message } from 'element-ui';
const taskLibraryManage = {
  namespaced: true,
  state: {
    taskLib: {},
    timeOutRemind: {},
    tags: [],
    // 	外部指令发起列表 instruction
    taskExternalList: [],
    //	手工发起列表 manual
    taskHandList: [],
    // 定时发起列表 timing
    taskTimingList: [],
    // 表单项
    taskForm: {},

    allFieldsData: [],

    // 当前操作的数据  flowVariables 使用
    dealInfo: {
      relaId: '',
      relaName: ''
    },

    // 操作列表新增 事件注册
    eventRegistPool: {},

    // 直接监听eventRegistPool 不生效 不到为啥
    // 不到会不会有问题
    flag: true,

    formFlag: true, // 表单编辑显隐
    customForm: {
      // 自定义表单配置
      formList: []
    },
    formItem: '' // 自定义表单配置base64格式
  },
  getters: {
    flowVariables: (state) => {
      const customForm = []; // 表单
      let nodeVariableCdn = []; // 交互数据
      const launchVaribles = []; // 手工发起

      const flowInstanceCdn = {
        text: '任务实例',
        label: '任务实例',
        value: 'taskInstance',
        children: [
          {
            text: '任务Id',
            label: '任务Id',
            value: 'systemVariable.taskId'
          },
          {
            text: '任务名称',
            label: '任务名称',
            value: 'systemVariable.taskName'
          },
          {
            text: '任务开始时间',
            label: '任务开始时间',
            value: 'systemVariable.taskStartTime'
          },
          {
            text: '任务状态集合',
            label: '任务状态集合',
            value: 'systemVariable.taskStatus'
          }
        ]
      };

      function formFun() {
        formFunRecursion(state.customForm.formList, customForm, null);
        delChildren(customForm);
        function formFunRecursion(list, arr, parentKey) {
          list.forEach((item) => {
            let fieldType = 0;
            switch (item.type) {
              case 'upload':
                fieldType = 5;
                break;
              case 'date':
                fieldType = 2;
                break;
              case 'number':
                fieldType = 1;
                break;
              case 'hidden':
                fieldType = 6;
                break;
            }
            const vars = {
              text: item.label,
              label: item.label,
              value: parentKey
                ? `formVariable.${parentKey}.${item.key}`
                : `formVariable.${item.key}`,
              fieldType,
              children: []
            };

            if (item.type === 'table') {
              if (item.origId) {
                vars.origId = item.origId;
              }
              vars.children = item.cols.map((col) => {
                return {
                  ...col,
                  fieldCode: `formVariable.${item.key}.${col.prop}`,
                  value: parentKey
                    ? `formVariable.${parentKey}.${item.key}.${col.prop}`
                    : `formVariable.${item.key}.${col.prop}`,
                  fieldType: item.fieldType === 'date' ? 2 : 0
                };
              });
            }
            if (item.type === 'childFrom' && item.value.formList) {
              formFunRecursion(item.value.formList, vars.children, item.key);
            }
            arr.push(vars);
          });
        }
        function delChildren(arr) {
          arr.forEach((item) => {
            if (
              Object.prototype.hasOwnProperty.call(item, 'children') &&
              item.children.length > 0
            ) {
              delChildren(item.children);
            } else {
              delete item.children;
            }
          });
        }
      }

      function nodeVariableFun() {
        const nodeVariableCdnAll = [];
        const data = state.eventRegistPool;
        for (const i in data) {
          const arr = [];
          if (data[i]) {
            data[i].forEach((item) => {
              if (item.eventType * 1 === 2) {
                const preCode = `interactiveVariable.${item.eventCode}`;
                if (!arrayFind(arr, 'value', preCode)) {
                  const ps = arrayFind(
                    state.allFieldsData,
                    'id',
                    item.dataViewId
                  );
                  if (ps) {
                    const viewArr = ps.fields.map((field) => {
                      return {
                        text: field.fieldName,
                        label: field.fieldName,
                        value: `${preCode}.${field.fieldCode}`,
                        fieldType: field.fieldType
                      };
                    });
                    arr.push({
                      text: item.registerName,
                      label: item.registerName,
                      value: preCode,
                      children: viewArr,
                      origId: item.id
                    });
                  }
                }
              }
            });
            nodeVariableCdnAll.push({
              text: state.dealInfo.relaName,
              label: state.dealInfo.relaName,
              value: state.dealInfo.id,
              children: arr
            });
          }
        }

        nodeVariableCdn = nodeVariableCdnAll.filter(
          (item) => item.children.length > 0
        );
      }

      // 手工发起处理
      function taskHandListFun() {
        const trans = (lau) => {
          const preCode = `interactiveVariable.${lau.handCode}`;
          let viewArr = [];
          if (!lau['accessFlags']) {
            const ps = arrayFind(state.allFieldsData, 'id', lau.dataViewId);
            if (ps) {
              viewArr = ps.fields.map((field) => {
                return {
                  text: field.fieldName,
                  label: field.fieldName,
                  value: `${preCode}.${field.fieldCode}`,
                  fieldType: field.fieldType
                };
              });
            }
          }

          if (viewArr.length > 0) {
            launchVaribles.push({
              text: lau.handName,
              label: lau.handName,
              value: preCode,
              origId: lau.dataViewId,
              children: viewArr
            });
          }
        };
        state.taskExternalList.forEach(trans);
        state.taskHandList.forEach(trans);
        state.taskTimingList.forEach(trans);
      }

      formFun();
      nodeVariableFun();
      taskHandListFun();

      const flowVariablesArr = [];
      if (customForm.length > 0) {
        flowVariablesArr.push({
          text: '流程表单',
          label: '流程表单',
          value: 'form',
          children: customForm
        });
      }
      if (nodeVariableCdn.length > 0) {
        flowVariablesArr.push({
          text: '交互数据',
          label: '交互数据',
          value: 'nodeVariable',
          children: nodeVariableCdn
        });
      }
      if (launchVaribles.length > 0) {
        flowVariablesArr.push({
          text: '发起指标',
          label: '发起指标',
          value: 'launchVaribles',
          children: launchVaribles
        });
      }
      flowVariablesArr.push(flowInstanceCdn);

      return flowVariablesArr;
    }
  },
  mutations: {
    //基本信息
    UPDATE_BASICINFO: (state, data) => {
      state.taskLib = data;
    },
    // 任务提醒设置
    UPDATETIMEOUTREMIND: (state, data) => {
      state.timeOutRemind = data;
    },
    //基本信息Id
    UPDATEId: (state, data) => {
      state.taskLib.id = data;
    },
    //基本信息标签
    UPDATETAGS: (state, data) => {
      state.tags = data;
    },

    // 发起方式 修改
    UPDATE_LAUNCHING: (state, { type, data, editIndex }) => {
      // state.taskLaunching[type] = data;
      if (editIndex === -1) {
        state[type].push(data);
      } else {
        state[type].splice(editIndex, 1, data);
      }
    },
    // 初始话发起方式
    INIT_LAUNCHING: (state, { type, data }) => {
      state[type] = data;
    },
    // 删除发起方式
    DEL_LAUNCHING: (state, { type, index }) => {
      state[type].splice(index, 1);
    },

    //基本信息标签
    UPDATETASKFORM: (state, data) => {
      state.taskForm = data;
    },

    // 初始话数据
    INITEVENTREGISTPOOL: (state, data) => {
      data = data ? data : {};
      state.eventRegistPool = data;
    },

    // 修改数据
    UPDATEEVENTREGISTPOOL: (state, { type, data, addIndex }) => {
      state.flag = !state.flag;
      if (data) {
        if (addIndex === -1) {
          if (!state.eventRegistPool[type]) {
            state.eventRegistPool[type] = [];
          }
          state.eventRegistPool[type].push(data);
        } else {
          state.eventRegistPool[type].splice(addIndex, 1, data);
        }
      } else {
        state.eventRegistPool[type].splice(addIndex, 1);
      }
    },

    UPDATEDEALINFO: (state, info) => {
      state.dealInfo = info;
    },
    CHANGEFORMFLAG: (state, val = '') => {
      state.formFlag = val !== '' ? val : !state.formFlag;
    },
    CUSTOM_FORM: (state, customForm) => {
      state.customForm = customForm;
    },
    FORM_ITEM: (state, formItem) => {
      state.formItem = formItem;
    },

    ALLFIELDSDATAFUN: (state, allFieldsData) => {
      state.allFieldsData = allFieldsData;
    }
  },
  actions: {
    saveBasicInfo({ commit }, params) {
      commit('UPDATE_BASICINFO', params);
    },
    saveTask({ commit }, val) {
      return new Promise((resolve, rej) => {
        const {
          taskLib,
          taskExternalList,
          taskHandList,
          taskTimingList,
          tags,
          timeOutRemind,
          taskForm
        } = this.state.taskLibraryManage;
        if (
          !taskExternalList.length &&
          !taskHandList.length &&
          !taskTimingList.length
        ) {
          Message({
            message: '请确认存在发起方式',
            type: 'error',
            duration: 2000
          });
          rej();
        } else {
          const taskFormString = JSON.stringify(taskForm);
          console.log(taskFormString);
          if (val.scenesName) {
            const data = {
              taskInfoRequest: {
                taskLib,
                tags,
                timeOutRemind,
                taskForm: taskFormString,
                taskLaunching: {
                  taskExternalList,
                  taskHandList,
                  taskTimingList
                }
              },
              ...val
            };
            addSceneTask(data).then((res) => {
              resolve(res);
            });
          } else {
            const data = {
              taskLib: taskLib,
              tags: tags,
              timeOutRemind,
              taskForm: taskFormString,
              taskLaunching: {
                taskExternalList,
                taskHandList,
                taskTimingList
              }
            };
            if (taskLib.id) {
              editTask(data).then((res) => {
                resolve(res);
              });
            } else {
              addTask(data).then((res) => {
                resolve(res);
              });
            }
          }
        }
      });
    },

    updateForm({ commit }, form) {
      commit('FROM_UPDATE', form);
    },
    getFormItem({ commit }, formItem) {
      console.log('FORM_ITEM', formItem);
      commit('FORM_ITEM', formItem);
    },

    getCustomForm({ commit }, form) {
      console.log('CUSTOM_FORM', form);
      commit('CUSTOM_FORM', form);
    },

    getallFieldsData({ commit }) {
      dataViewFields().then(({ data, error }) => {
        if (!error) {
          dataViewFields({ type: '1' }).then((res) => {
            commit('ALLFIELDSDATAFUN', [...data, ...res.data]);
          });
        }
      });
    }
  }
};
export default taskLibraryManage;
