<template>
  <div class="table-panel">
    <base-panel :title="title" :actions="rightBtns" :no-title="noTitle">
      <el-row class="basicQuery">
        <div class="default-slot">
          <slot></slot>
        </div>
        <el-col :span="queryBarWidth">
          <slot name="operate-left"></slot>
        </el-col>
        <el-col :span="24 - queryBarWidth">
          <div class="right">
            <slot name="operate-right"> </slot>
            <authorize
              v-for="operate in operations"
              :key="operate.title"
              :code="operate.code"
            >
              <el-button
                v-show="!operate.show"
                :class="[operate.iconClass ? 'iconBtn' : '']"
                :icon="operate.icon"
                :disabled="btnDisabled(operate.disabled)"
                @click="operate.onClick"
              >
                <svg-icon
                  v-if="operate.iconClass"
                  :icon-class="operate.iconClass"
                ></svg-icon>
                {{ operate.title }}
              </el-button>
            </authorize>
          </div>
        </el-col>
      </el-row>
      <!-- 高级筛选里的内容 -->
      <el-collapse-transition>
        <slot name="transition-query-box"></slot>
        <advanced-screening
          v-if="searcher"
          :spread="searcher.visible"
          :model="searcher.model"
          :items="searcher.items"
          :class-name="searcher.className"
          :screen-loading="searcher.loading"
          label-width="30%"
          @doQuery="handleQuery"
          @doReset="handleReset"
        ></advanced-screening>
      </el-collapse-transition>
      <div v-show="tipsBar" class="tips-bar">
        <slot name="tips-bar"></slot>
      </div>
      <div class="inlineStyle">
        <BaseTable
          ref="baseTablePanel"
          class="sortTable"
          border
          :row-key="rowKey"
          :default-expand-all="defaultExpandAll"
          :tree-props="treeProps"
          :border-style="borderStyle"
          :data-source="dataSource"
          :columns="columns"
          :loading="loading"
          :lazyload="lazyload"
          :stripe="stripe"
          :row-style="rowStyle"
          :has-pagination="hasPagination"
          :layout="layout ? layout : `total, sizes, prev, pager, next, jumper`"
          :page-size="pageSize"
          :current-page="currentPage"
          :show-summary="showSummary"
          :cell-class-name="cellClassName"
          :row-class-name="rowClassName"
          :summary-method="summaryMethod"
          :default-height="defaultHeight"
          :render-header="renderHeader"
          :height="height"
          :can-edit="canEdit"
          v-bind="$attrs"
          v-on="$listeners"
          @cell-click="cellClick"
          @select="select"
          @select-all="selectAll"
          @selection-change="selectionChange"
        ></BaseTable>
      </div>
    </base-panel>
  </div>
</template>

<script>
import BaseTable from '../BaseTable';
import AdvancedScreening from './AdvancedScreening.vue';
import dragColumns from '@mixins/dragColumns';
export default {
  name: 'BaseTablePanel',
  components: {
    BaseTable,
    'advanced-screening': AdvancedScreening
  },
  mixins: [dragColumns],
  props: {
    title: {
      type: String,
      default: () => ''
    },
    // 卡片无标题
    noTitle: {
      type: Boolean,
      default: false
    },
    tableCode: {
      type: String,
      default: () => ''
    },
    treeProps: {
      type: Object,
      default: () => {}
    },
    rowKey: {
      type: String,
      default: () => ''
    },
    defaultExpandAll: {
      type: Boolean,
      default: false
    },
    queryBarWidth: {
      type: Number,
      default: () => 24
    },
    show: {
      type: Boolean,
      default: false
    },
    rightBtns: {
      type: Array,
      default: () => []
    },
    operations: {
      type: Array,
      default: () => []
    },
    dataSource: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    },
    hasPagination: {
      type: Boolean,
      default: false
    },
    currentPage: {
      type: Number,
      default: () => 1
    },
    pageSize: {
      type: Number,
      default: () => 20
    },
    loading: {
      type: Boolean,
      default: false
    },
    layout: {
      type: String,
      default: () => ''
    },
    config: {
      type: Object,
      default: () => {}
    },
    defaultHeight: {
      type: Boolean,
      default: false
    },
    height: {
      type: Number,
      default: () => 0
    },
    render: {
      type: Function,
      default: () => function() {}
    },
    stripe: {
      type: Boolean,
      default: false
    },
    borderStyle: {
      type: Boolean,
      default: false
    },
    cellClassName: {
      type: Function,
      default: () => function() {}
    },
    rowClassName: {
      type: Function,
      default() {
        return 'row';
      }
    },
    summaryMethod: {
      type: Function,
      default: () => function() {}
    },
    showSummary: {
      type: Boolean,
      default: false
    },
    tableKey: {
      type: Number,
      default: () => 1
    },
    searcher: {
      type: Object,
      default: () => null
    },
    tipsBar: {
      type: Boolean,
      default: false
    },
    rowStyle: {
      type: Function,
      default() {
        return 'row';
      }
    },
    lazyload: {
      type: Boolean,
      default: false
    },
    canEdit: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    btnDisabled(val) {
      if (typeof val === 'function') {
        return val();
      } else {
        return val;
      }
    },
    handleQuery() {
      this.$emit('doQuery');
    },
    handleReset() {
      this.$emit('doReset');
    },
    cellClick(row, column, cell, event) {
      this.$emit('cellClick', row, column);
    },
    select(selection, row) {
      this.$emit('select', selection, row);
    },
    selectAll(selection) {
      this.$emit('selectAll', selection);
    },
    selectionChange(selection) {
      this.$emit('selectionChange', selection);
    },
    toggleRowSelection(row, selected) {
      this.$refs.baseTablePanel.toggleRowSelection(row, selected);
    },
    clearSelection() {
      this.$refs.baseTablePanel.clearSelection();
    }
  }
};
</script>

<style lang="scss" scoped>
.table-panel {
  position: relative;
  padding: 0px;
  background-color: $color-white;
  border: 1px solid $color-panel-boder;
  .inlineStyle {
    padding-top: 20px;
  }
}
.noTableBorder {
  border: none;
}
</style>
<style lang="scss">
.el-scrollbar__view {
  .tags-view-container {
    .tags-view-item {
      height: 36px;
      .title {
        font-size: 12px;
      }
    }
  }
}
.basicQuery {
  .smallSelect {
    width: 24%;
    min-width: 140px;
    max-width: 240px;
  }
  .el-button--primary {
    color: #fff;
    background-color: $color-main;
    border-color: $color-main;
  }
  .selectTitle {
    font-size: 14px;
    color: $color-mainfont;
  }
  .pageTitle {
    font-size: 16px;
    font-weight: bold;
    color: $color-mainfont;
  }
  .smallSelect + .smallSelect {
    margin-left: 10px;
  }
  .pageTitle + .basicQueryTitle,
  .smallSelect + .basicQueryTitle,
  .basicQueryBtn + .basicQueryTitle,
  .smallSelect + .basicQueryBtn {
    margin-left: 20px;
  }
  .smallSelect + .selectTitle {
    margin-left: 40px;
  }

  /* 查询条件里，条件间间距为40px，采用form表单布局，去掉每个item10px的右margin */
  .formItemtTitle {
    margin-right: 0;
  }
  .formItemtTitle + .formItemtTitle {
    margin-left: 40px;
  }

  /* 用form表单布局的，将不再识别 smallSelect + .basicQueryBtn的样式 ，所以为查询按钮加以下类名 */
  .authorizeBtn {
    margin-left: 20px;
  }

  /* 查询条件和高级查询里的输入框或查询框宽度为224px */
  .filterWidth {
    width: 224px;
  }

  /* 修改结束 */
  .right {
    text-align: right;
    .iconBtn {
      padding: 10px;
      font-size: 14px;
    }
  }

  .operate-left-form {
    background-color: $color-back-header;
    transition: background-color 0.8s;
    border: 1px solid $color-table-cutting;
    padding: 20px 20px 10px 20px;

    .el-form-item {
      margin-bottom: 10px;
    }
  }
}
.transitionBox {
  position: relative;
  padding: 10px 10px 0 10px;
  margin: 8px 0 0 0;
  border: 1px solid $color-main;

  /* <!--&::after {--> */

  /* <!--position: absolute;--> */

  /* <!--top: -7px;--> */

  /* <!--left: 26px;--> */

  /* <!--display: block;--> */

  /* <!--width: 12px;--> */

  /* <!--height: 12px;--> */

  /* <!--content: '';--> */

  /* <!--background-color: $color-white;--> */

  /* <!--border: 1px solid $color-main;--> */

  /* <!--border-right: none;--> */

  /* <!--border-bottom: none;--> */

  /* <!--transform: rotate(45deg);--> */

  /* <!--}--> */
  .grid {
    .box {
      float: left;
      width: (100% / 5);
      padding-right: 10px;
      .el-form-item {
        position: relative;
        margin-bottom: 10px;
      }
    }
    // 报表管理 高级筛选中   截止时间宽度为上边两个搜索条件宽度的二倍
    .box1 {
      float: left;
      width: (200% / 5);
      padding-right: 10px;
      .el-form-item {
        position: relative;
        margin-bottom: 10px;
      }
    }
    .longData .el-tag:first-child {
      max-width: calc(100% - 72px);
      .el-select__tags-text {
        display: inline-block;
        width: calc(100% - 18px);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .el-icon-close {
        top: -7px;
      }
    }
    .cutOut .el-select__tags .el-tag:first-child {
      max-width: 120px;
      .el-select__tags-text {
        display: inline-flex;
        width: 86px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .specialBox .el-form-item {
      margin-bottom: 20px;
    }
    .boxBtn1 {
      .el-form-item__content {
        margin-left: 30px !important;
      }
    }
    .boxBtn2 {
      float: right;
      // width: 20%;
      padding-right: 10px;
      text-align: right;
      .el-form-item__content {
        margin-left: 0 !important;
      }
    }
    .boxBtn3 {
      float: left;
      width: 100%;
      text-align: center;
      .el-form-item__content {
        margin-left: 0 !important;
      }
    }
    &::after {
      display: block;
      clear: both;
      visibility: hidden;
      content: '';
    }
  }
  .el-select,
  .el-date-editor {
    width: 100%;
  }
  .el-form-item__label {
    position: absolute;
    top: 50%;
    line-height: 20px;
    transform: translate(0%, -50%);
  }
}
.tips-bar {
  margin-top: 10px;
  font-size: 14px;
  color: $color-mainfont;
  .redTips {
    border: 1px solid $color-status-danger;
    border-radius: 2px;
  }
  .tips-bar-header {
    height: 38px;
    padding: 0 10px;
    line-height: 38px;
    background-color: rgba($color-status-danger, 0.1);
    .redTipsIcon {
      color: $color-status-danger;
    }
    .redTipsRight {
      text-align: right;
      .openIcon {
        padding-left: 4px;
      }
    }
  }
  .tips-bar-body {
    max-height: 242px;
    padding: 0 10px;
    overflow-y: auto;
    line-height: 40px;
    border-top: 1px solid $color-status-danger;
    .bodyTitle {
      font-weight: bold;
    }
    .bodyBox + .bodyBox {
      border-top: 1px solid $color-line-button;
    }
  }
}
</style>
