import Layout from '@layouts/main';

export default [
  {
    path: '/processCenter',
    component: Layout,
    meta: {
      title: '流程中心',
      authority: 'processCenter',
      icon: 'flow'
    },
    order: 4,
    redirect: '/processCenter/',
    children: [
      {
        path: 'processConfig',
        name: 'processConfig',
        meta: {
          title: '流程模板管理',
          icon: 'stack-line',
          authority: 'processConfig'
        },
        component: () => import('@views/processConfig/index.vue')
      },
      {
        path: 'processNode',
        name: 'processNode',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '流程节点配置',
          authority: 'processNode',
          parentInfo: {
            meta: {
              title: '流程模板管理',
              clickable: true
            },
            path: 'processConfig'
          }
        },
        component: () => import('@views/processConfig/processNode/index.vue')
      },
      {
        path: 'formControl',
        name: 'formControl',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '表单控制',
          authority: 'formControl',
          parentInfo: {
            meta: {
              title: '流程模板管理',
              clickable: true
            },
            path: 'processConfig'
          }
        },
        component: () =>
          import('@views/processConfig/AddConfig/formControl.vue')
      },
      {
        path: 'customForm',
        name: 'customForm',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '流程表单配置',
          authority: 'customForm',
          parentInfo: {
            meta: {
              title: '流程模板管理',
              clickable: true
            },
            path: 'processConfig'
          }
        },
        component: () =>
          import('@views/processConfig/customForm/edit/index.vue')
      },
      {
        path: 'processAssignment',
        name: 'processAssignment',
        meta: {
          title: '流程任务管理',
          icon: 'task',
          authority: 'processAssignment'
        },
        component: () => import('@views/processAssignment/index.vue')
      },
      {
        path: 'taskDeal',
        name: 'taskDeal',
        meta: {
          title: '任务办理',
          ignoreAuth: true,
          hidden: true,
          authority: 'processAssignment',
          parentInfo: {
            meta: {
              title: '流程任务管理',
              clickable: true
            },
            path: 'processAssignment'
          }
        },
        component: () => import('@views/processAssignment/taskDeal.vue')
      },
      {
        path: 'historyAssignment',
        name: 'historyAssignment',
        meta: {
          title: '历史任务查看',
          icon: 'task_history',
          authority: 'historyAssignment'
        },
        component: () =>
          import('@views/processAssignment/historyAssignment.vue')
      }
    ]
  }
];
