const authority = {
  state: {
    checkValue: [],
    selectedUser: {},
    selectedRole: {},
    selectedGroup: {},
    menuTrees: [],
    selectedActions: [],
    selectedMenus: [],
    menuSelectedActions: {},

    freshManagement: false
  },
  mutations: {
    updateActions: (state, data) => {
      state.checkValue = data;
    },
    changeSelectedUser: (state, user) => {
      state.selectedUser = user;
    },
    changeSelectedGroupAdd: (state, groupAdd) => {
      state.selectedGroupAdd = groupAdd;
    },
    changeSelectedRole: (state, role) => {
      state.selectedRole = role;
    },
    changeSelectedGroup: (state, group) => {
      state.selectedGroup = group;
    },
    changeMenuTrees: (state, trees) => {
      state.menuTrees = trees;
    },
    changeSelectedActions: (state, actions) => {
      state.selectedActions = actions;
    },
    changeSelectedMenus: (state, menus) => {
      state.selectedMenus = menus;
    },
    changeMsas: (state, msas) => {
      state.menuSelectedActions = msas;
    },
    freshManage: (state) => {
      state.freshManagement = !state.freshManagement;
    }
  },
  actions: {
    updateActions({ commit }, data) {
      commit('updateActions', data);
    },
    freshManage({ commit }) {
      commit('freshManage');
    }
  },
  getters: {
    getChechValue(state) {
      return state.checkValue;
    }
  }
};

export default authority;
