import { Message } from 'element-ui';
import { getLayoutTemplate } from '@api/pageTemplate';
import { dataViewFields } from '@api/viewData';

const pageTemplate = {
  namespaced: true,
  state: {
    layoutList: [], // 布局模板列表
    editFlag: false, // 页面是否被编辑
    dataSetList: [], // 数据集及相应字段、参数字段的数据
    activeBlock: {} // 当前内容块
  },
  mutations: {
    UPDATE_LAYOUT_LIST: (state, data) => {
      state.layoutList = data;
    },
    UPDATE_EDIT_FLAG: (state, data) => {
      state.editFlag = data;
    },
    UPDATE_DATASET_LIST: (state, data) => {
      state.dataSetList = data;
    },
    UPDATE_ACTIVE_BLOCK: (state, data) => {
      state.activeBlock = data;
    }
  },
  actions: {
    // 获取布局模板列表
    getLayoutTemplateList({ commit }) {
      getLayoutTemplate().then(({ data, error }) => {
        if (data) {
          commit('UPDATE_LAYOUT_LIST', data);
        }
        if (error) {
          Message.error(error.message || '获取数据失败');
        }
      });
    },
    // 获取数据集列表
    getDataSet({ commit }) {
      return new Promise((resolve) => {
        dataViewFields({ status: '0' }).then(({ data, error }) => {
          if (data) {
            commit('UPDATE_DATASET_LIST', data);
            resolve();
          } else {
            Message.error(error.message || '获取数据失败');
          }
        });
      });
    }
  }
};

export default pageTemplate;
