import {
  handleLogin,
  fetchPermissions,
  logout,
  mailQuery
} from '../../api/user';
import { setSessionStorage, removeSessionStorage } from '../../utils/storage';
import { Message } from 'element-ui';
import cloneDeep from 'lodash/cloneDeep';
//  let refreshInterval = null;

const authToken = 'auth_token';

function initMenuAuthorize(menus, parentCode, parentRouterCode) {
  menus.forEach((item) => {
    item.routerCode = parentRouterCode
      ? parentRouterCode + '/' + item.code
      : '/' + item.code;
    if (parentCode) {
      item.authorize = `${parentCode}_${item.code}`;
    }
    if (item.children && item.children.length > 0) {
      initMenuAuthorize(item.children, item.code, item.routerCode);
    }

    // 目前用href代表是通过页面模板管理新注册的页面
    if (item.href) {
      item.routerCode = `/dynamicPage/${item.href}/${item.title}`;
      item.authorize = `dynamicPage`;
    }
  });
}

const user = {
  // namespaced: true, // 用于报表管理 发送人回显
  state: {
    userInfo: {
      name: 'loading...'
    },
    authorizes: null,
    assign: null,
    mailList: [],
    authSwitch: true,
    userStation: {}
  },

  mutations: {
    USER_INFO: (state, userInfo) => {
      state.userInfo = userInfo;
    },
    SET_AUTHORIZES: (state, authorizes) => {
      state.authorizes = authorizes;
    },
    SET_MAIL_LIST: (state, params) => {
      state.mailList = params;
    },
    UPDATE_ASSIGN: (state, assign) => {
      state.assign = assign;
    },
    SET_USER_STATION: (state, station) => {
      state.userStation = station;
    }
  },

  actions: {
    login({ dispatch, commit }, userInfo) {
      return new Promise((resolve, reject) => {
        handleLogin(userInfo)
          .then(({ data }) => {
            console.log(data);
            // 登录校验：判断是否登录成功
            if (data.state == 20101 || data.state == 20104) {
              commit('USER_INFO', {
                name: data.userInfo.name,
                usercode: data.userInfo.code,
                id: data.userInfo.id
              });

              commit('RESET_CACHE_PARAMS'); //登录 清除缓存功能所缓存的数据

              if (data.accessToken) {
                setSessionStorage(authToken, data.accessToken);
                if (data.menuTree) {
                  initMenuAuthorize(data.menuTree, null, null);
                  commit('SET_AUTHORIZES', data.menuTree);
                }
                //获取未读消息
                mailQuery({ receiveId: data.userInfo.code }).then(
                  ({ data, error }) => {
                    if (!error) {
                      if (data.rows) {
                        commit('SET_MAIL_LIST', data.rows);
                      }
                    } else {
                      Message.error(error.message);
                    }
                  }
                );
                resolve(data.state);
              } else {
                resolve(data.state);
              }
            } else {
              resolve(data.state);
            }
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // authVerify({
    //     commit
    // }, getUserToken) {
    //     authVerify(getUserToken).then(({
    //         data
    //     }) => {
    //         commit('USER_INFO', data);
    //     });
    // },
    // 登出
    logOut({ commit }, _self) {
      logout().then(() => {
        // $auth.removeToken();    // 登出时删除cookies中的用户名
        removeSessionStorage(authToken);
        removeSessionStorage('vuex');
        commit('USER_INFO', {
          name: 'loading...'
        });
        commit('SET_AUTHORIZES', null);
        // commit('SET_CHECKED_MENU_LIST', {});
        commit('UPDATE_ASSIGN', null);
        commit('SET_MAIL_LIST', []);
        commit('tagsView/DEL_ALL_VISITED_VIEWS');
        commit('tagsView/DEL_ALL_CACHED_VIEWS');
        if (_self) {
          _self.$router.push({
            path: '/login'
          });
        }
        // _self.$router.push({path: '/login'});
      });
    },

    fetchAuthorizes({ commit, state }) {
      return new Promise((resolve, reject) => {
        const param = {
          id: state.userInfo.id
        };
        fetchPermissions(param)
          .then(({ data }) => {
            console.log(data);
            if (data && data.length > 0) {
              initMenuAuthorize(data, null, null);
            }
            commit('SET_AUTHORIZES', data);
            // commit('changeIsRouter', true);
            resolve();
          })
          .catch();
      });
    }
  }
};

export default user;
