<template>
  <el-table-column v-if="col.render" :key="col.dataIndex" v-bind="col">
    <template slot-scope="{ row, column, $index }">
      <ColumnRender
        :render-col="col.render"
        :record="row"
        :value="row[col.dataIndex]"
        :index="$index"
        :column="column"
        :prop-key="col.prop"
      ></ColumnRender>
    </template>
  </el-table-column>
  <el-table-column
    v-else-if="col.label === '操作' && col.actions"
    :key="col.prop"
    v-bind="col"
  >
    <template slot-scope="{ row, column, $index }">
      <ColumnRender
        :render-col="renderAction(col.actions)"
        :record="row"
        :value="row[col.dataIndex]"
        :index="$index"
        :prop-key="col.prop"
      ></ColumnRender>
    </template>
  </el-table-column>
  <el-table-column
    v-else-if="canEdit && col.editer"
    :key="col.prop"
    v-bind="col"
  >
    <template slot-scope="{ row, column, $index }">
      <TableColumnEdit
        v-if="
          row.edits[columnIndex] === 1 ||
            row.isPrimaryKey ||
            col.editer.type != 'input'
        "
        :spread="true"
        :save="row.save"
        :model="row[col.prop]"
        :record="row"
        :editer="col.editer"
        @editchange="editchange(column)"
        @editerBlur="handleEdit(row, $index, 0, col.prop)"
        @inputChange="(val) => handleInput(row, column.property, val)"
      >
      </TableColumnEdit>
      <div v-else>
        <span v-if="col.editer.type === 'select'">{{
          editSelectFormatter(col.editer.config, row[col.prop])
        }}</span>
        <span v-else>{{
          col.formatter
            ? col.formatter(row, column, row[col.prop])
            : row[col.prop]
        }}</span>
        <el-button
          v-if="!row.disable || row.disable[columnIndex] !== 0"
          class="button_style"
          type="text"
          :icon="editIcon"
          @click="handleEdit(row, $index, 1, col.prop)"
        ></el-button>
      </div>
    </template>
  </el-table-column>
  <el-table-column v-else :key="col.prop" v-bind="col">
    <template v-if="col.children">
      <table-column
        v-for="(item, index) in col.children"
        :key="index"
        :col="item"
        :render-header="
          item.lineFeed === true ? lineFeedHandle(item) : item.renderHeader
        "
        :formatter="
          item.format || item.format === ''
            ? formatNumber(item)
            : item.formatter
        "
      ></table-column>
    </template>
  </el-table-column>
</template>

<script>
import TableColumnEdit from './TableColumnEdit';
import { currencyFormatter, arrayFind } from '@utils/util';
import { authActionRender } from '../renders/colActions';
import ColumnRender from './column-render';
import random from 'lodash/random';
export default {
  name: 'TableColumn',
  components: {
    ColumnRender,
    TableColumnEdit
  },
  props: {
    col: {
      type: Object,
      default: () => {}
    },
    columnIndex: {
      type: Number,
      default: () => 0
    },
    dataTable: {
      type: Number,
      default: () => []
    },
    canEdit: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    editIcon: function() {
      if (this.canEdit && this.col.editer) {
        let icon = 'el-icon-edit';
        if (this.col.editer.type === 'date') {
          icon = 'el-icon-date';
        } else if (this.col.editer.type === 'select') {
          icon = 'el-icon-caret-bottom';
        }
        return icon;
      } else {
        return null;
      }
    }
  },
  methods: {
    handleInput(row, colProp, val) {
      this.$emit('doHandleInput', row, colProp, val);
    },
    handleEdit(row, index, flag, prop) {
      row.edits[this.columnIndex] = flag;
      // 处理字段不存在的情况
      if (!row[prop]) {
        row[prop] = '';
      }
      this.$set(row, 'edits', new Array(...row.edits));
      this.$emit('doEdit', row, index, this.columnIndex);
    },
    // 添加
    addChange(record) {
      this.$emit('addEdit', record);
    },
    // 删除
    deleteButton(record) {
      this.$emit('deleteEdit', record);
    },
    renderAction(actions) {
      return authActionRender(actions);
    },
    formatNumber(col) {
      col.render = (h, scope) => {
        col.format = col.format === '' ? '2' : col.format;
        return (
          <span>{`${currencyFormatter(
            scope.record[scope.prop],
            Number(col.format)
          )}`}</span>
        );
      };
    },
    editSelectFormatter(config, val) {
      let opl = 'label';
      let opv = 'value';
      if (config.optionConfig) {
        if (config.optionConfig.label) {
          opl = config.optionConfig.label;
        }
        if (config.optionConfig.value) {
          opv = config.optionConfig.value;
        }
      }
      const opt = arrayFind(config.options, opv, val);
      return opt ? opt[opl] : '';
    },
    lineFeedHandle(col) {
      col.renderHeader = (h, { column, $index }) => {
        return h(
          'span',
          {
            style: {
              display: 'inline-block',
              'vertical-align': 'middle',
              'line-height': '18px'
            }
          },
          [
            h('span', {}, column.label.split('|')[0]),
            h(
              'span',
              {
                style: { display: 'block' }
              },
              column.label.split('|')[1]
            )
          ]
        );
      };
    }
  }
};
</script>
<style lang="scss" scoped>
.button_style {
  float: right;
  color: #bfc3cc !important;
}
</style>
