import { getAvatar } from '@api/personal';
const personalInfo = {
  namespaced: true,
  state: {
    avatar: null
  },
  mutations: {
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar;
    }
  },
  actions: {
    // 获取头像
    getAvatar({ commit }) {
      getAvatar().then((res) => {
        const avatar = res.data ? 'data:image/jpeg;base64,' + res.data : null;
        // console.log(avatar);
        commit('SET_AVATAR', avatar);
      });
    }
  }
};
export default personalInfo;
