import Layout from '@layouts/main';

export default [
  {
    path: '/dynamicPage',
    component: Layout,
    meta: {
      authority: 'dynamicPage',
      title: '自定义页面',
      icon: 'new-page'
    },
    order: 5,
    redirect: '/dynamicPage/',
    children: [
      {
        path: ':id/:dynamicPageTitle',
        name: 'dynamicPage',
        meta: {
          title: '自定义页面',
          authority: 'dynamicPage'
        },
        component: () => import('@views/pageTemplate/dynamicPage.vue')
      }
    ]
  }
];
