export default {
  name: 'ItemEdit',
  props: {
    type: String,
    config: Object,
    record: Object
  },
  render: function(h) {
    const self = this;
    switch (this.type) {
      case 'select':
        return selectItem(h, this.config, this.record);
      case 'input':
        return inputItem(h, this.config, this.record, self);
      case 'date':
        return dateItem(h, this.config, this.record);
      case 'time':
        return timeItem(h, this.config, this.record);
      case 'inputNumber':
        return inputNumberItem(h, this.config, this.record);
      case 'upload':
        return uploadItem(h, this.config, this.record);
      case 'cascader':
        return cascaderItem(h, this.config, this.record);
      case 'switch':
        return switchItem(h, this.config, this.record);
      case 'radioGroup':
        return radioItem(h, this.config, this.record);
      case 'checkboxGroup':
        return checkBoxItem(h, this.config, this.record);
      case 'singleCheck':
        return singleCheckBox(h, this.config, this.record);

      default:
        return '';
    }
  }
};

// 单选框组
function radioItem(h, config, record, self) {
  const {
    // model,
    key,
    props,
    listeners,
    options,
    optionConfig,
    syncConfig,
    optionsList,
    optionsType
  } = config;
  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }
  const opts = optionsType === 'viewsData' ? optionsList : options || [];
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h('el-radio', {
        key: option[opv],
        props: {
          label: option[opl] || option['text'],
          value: option[opv],
          disabled: option.disabled
        }
      });
    });
  }
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  return h(
    'el-radio-group',
    {
      props: {
        ...pps,
        value: record[key]
      },
      style: {
        width: '100%'
      },
      on: {
        ...on,
        input: (val) => {
          console.log(val);
          record[key] = val;
        }
      }
    },
    ops
  );
}
// 多选框组
function checkBoxItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners,
    options,
    optionsType,
    optionConfig,
    optionsList,
    syncConfig
  } = config;
  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }
  const opts = optionsType === 'viewsData' ? optionsList : options || [];
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h('el-checkbox', {
        key: option[opv],
        props: {
          label: option[opl] || option['text'],
          value: option[opv],
          disabled: option.disabled
        },
        on: {
          change(val, e) {
            let valueArray = record[key];
            const value = e.currentTarget.value;
            if (Array.isArray(valueArray)) {
              if (valueArray.includes(value)) {
                valueArray.splice(valueArray.indexOf(value), 1);
              } else {
                valueArray.push(value);
              }
            } else {
              valueArray = [value];
            }
            record[key] = valueArray;
            console.log(val, e);
          }
        }
      });
    });
  }

  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }

  return h(
    'el-checkbox-group',
    {
      props: {
        ...pps,
        value: record[key]
      },
      style: {
        width: '100%'
      },
      on: {
        ...on
      }
    },
    ops
  );
}

function selectItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners,
    options,
    optionsType,
    optionsList,
    optionConfig,
    placeholder,
    syncConfig
  } = config;
  let opl = 'label';
  let opv = 'value';
  if (optionConfig) {
    if (optionConfig.label) {
      opl = optionConfig.label;
    }
    if (optionConfig.value) {
      opv = optionConfig.value;
    }
  }

  const opts = optionsType === 'viewsData' ? optionsList : options() || [];
  let ops = [];
  if (opts) {
    ops = opts.map((option) => {
      return h('el-option', {
        key: option[opv],
        props: {
          label: option[opl] || option['text'],
          value: option[opv],
          disabled: option.disabled
        }
      });
    });
  }

  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }

  return h(
    'el-select',
    {
      props: {
        placeholder,
        ...pps,
        value: record[key]
      },
      style: {
        width: '100%'
      },
      on: {
        ...on,
        change: (val) => {
          record[key] = val;
          if (on.change) {
            on.change(val, record);
          }
        }
      }
    },
    ops
  );
}

function switchItem(h, config, record) {
  const { key, props, listeners, syncConfig } = config;
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  return h('el-switch', {
    props: {
      ...pps,
      value: record[key],
      disabled: pps.disabled
    },
    on: {
      ...on,
      change: (val) => {
        record[key] = val;
        if (on.change) {
          on.change(val, record);
        }
      }
    }
  });
}

// 单值多选框
function singleCheckBox(h, config, record) {
  const {
    key,
    props,
    listeners,
    syncConfig,
    singleCheckLabel,
    singleCheckproperty
  } = config;
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  return h('el-checkbox', {
    props: {
      ...pps,
      label: record[singleCheckLabel],
      value:
        typeof record[singleCheckproperty] === 'boolean' &&
        record[singleCheckproperty],
      disabled: pps.disabled
    },
    on: {
      ...on,
      change: (val) => {
        record[key] = val;
        if (on.change) {
          on.change(val, record);
        }
      }
    }
  });
}

function inputItem(h, config, record, self) {
  const {
    // model,
    key,
    props,
    listeners,
    placeholder,
    areaProps,
    syncConfig
  } = config;
  // console.log('model', model);
  const on = listeners || {};
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const area = areaProps || {};
  return h('el-input', {
    props: {
      ...pps,
      value: record[key],
      disabled: pps.disabled
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      ...area
    },
    on: {
      ...on,
      input: (val) => {
        record[key] = val;
        self.$emit('doInput', val);
        if (on.input) {
          on.input(val, record);
        }
      }
    }
  });
}

function dateItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners,
    placeholder,
    type,
    syncConfig
  } = config;
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const on = listeners || {};
  return h('el-date-picker', {
    props: {
      ...pps,
      valueFormat: 'yyyy-MM-dd',
      format: 'yyyy-MM-dd',
      value: record[key]
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      type: type || 'date'
    },
    on: {
      ...on,
      input: (val) => {
        record[key] = val;
        if (on.input) {
          on.input(val, record);
        }
      },
      blur: (val) => {
        val.value = record[key];
      }
    }
  });
}

function timeItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners,
    placeholder,
    type,
    syncConfig
  } = config;
  let pps = props || {};
  if (syncConfig) {
    pps = {
      ...pps,
      ...syncConfig()
    };
  }
  const on = listeners || {};
  return h('el-time-picker', {
    props: {
      ...pps,
      value: record[key]
    },
    attrs: {
      placeholder: placeholder || pps.placeholder || '请输入',
      type: type || 'date'
    },
    on: {
      ...on,
      input: (val) => {
        record[key] = val;
        if (on.input) {
          on.input(val, record);
        }
      }
    }
  });
}

function inputNumberItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners
  } = config;
  const pps = props || {};
  const on = listeners || {};
  return h('el-input-number', {
    props: {
      ...pps,
      value: record[key]
    },
    on: {
      change: (val) => {
        record[key] = val;
        if (on.input) {
          on.input(val);
        }
      }
    }
  });
}

function uploadItem(h, config, record) {
  const {
    // model,
    key,
    props,
    btnProps,
    listeners,
    iconProps
  } = config;
  const pps = props || {};
  const btnProp = btnProps || {};
  const iconProp = iconProps || {};
  const on = listeners || {};
  return h(
    'el-upload',
    {
      props: {
        ...pps,
        'file-list': record[key]
      },
      on: {
        ...on
      }
    },
    [
      h(
        'el-button',
        {
          props: {
            ...btnProp,
            size: btnProp.size || 'small'
          }
        },
        [
          h('svg-icon', {
            style: iconProp.styles,
            props: {
              iconClass: iconProp.iconClass
            }
          }),
          '上传'
        ]
      )
    ]
  );
}

function cascaderItem(h, config, record) {
  const {
    // model,
    key,
    props,
    listeners,
    options,
    optionProps
  } = config;
  const ops = options();
  const on = listeners || {};
  const pps = props || {};
  return h('el-cascader', {
    props: {
      value: record[key],
      ...pps,
      options: ops,
      props: optionProps
    },
    on: {
      change: (val) => {
        record[key] = val;
        if (on.change) {
          on.change(val);
        }
      }
    }
  });
}
