<template>
  <div class="breadcrumb">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <transition-group name="breadcrumb">
        <el-breadcrumb-item
          v-for="item in levelList"
          :key="item.path"
          :to="{ path: item.meta.clickable ? item.path : '' }"
        >
          <span :class="{ current: isCurrent(item.path) }">{{
            item.customTitle || item.meta.title
          }}</span>
        </el-breadcrumb-item>
      </transition-group>
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  data() {
    return {
      levelList: []
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    }
  },
  created() {
    this.getBreadcrumb();
  },
  methods: {
    getBreadcrumb() {
      const matched = this.$route.matched.filter(
        (item) => item.meta && item.meta.title && item.meta.title !== '首页'
      );
      const index = matched.findIndex((item) => {
        return item.meta.hidden === true;
      });
      if (index !== -1) {
        matched.splice(index, 0, matched[index].meta.parentInfo);
      }

      // 特殊处理：通过页面模板添加的菜单路由会有dynamicPageTitle参数，将这个参数作为面包屑的名称
      if (this.$route.params && this.$route.params.dynamicPageTitle) {
        matched[
          matched.length - 1
        ].customTitle = this.$route.params.dynamicPageTitle;
      }

      this.levelList = matched;
    },
    isCurrent(path) {
      return this.$route.path === path;
    }
  }
};
</script>

<style lang="scss" scoped>
.breadcrumb {
  // flex: 0 0 40px;
  padding-left: 20px;
  background-color: $color-back-page;
  transition: background-color 0.8s;
  .el-breadcrumb {
    display: inline-block;
    font-size: 14px;
    line-height: 32px;
    .current {
      color: #7f8ba7;
    }
  }
}
</style>
<style lang="scss">
.breadcrumb {
  .el-breadcrumb {
    .el-breadcrumb__inner,
    .el-breadcrumb__separator {
      font-weight: normal;
      color: #7f8ba7;
    }
  }
}
</style>
