import Layout from '@layouts/main';
import { lazyLoadView } from '../util';
import AuthorityManage from '@views/authorityManage/index';
export default [
  {
    path: '/authourityManage',
    component: Layout,
    //   添加hidden属性，在菜单中隐藏该模块，注释hidden即可显示
    // hidden:true,
    meta: {
      title: '系统管理',
      authority: 'authourityManage',
      icon: 'system'
    },
    order: 2,
    redirect: '/authourityManage/',
    children: [
      {
        path: 'authorityManage',
        name: 'authorityManage',
        component: AuthorityManage,
        meta: {
          title: '权限管理',
          icon: 'auth',
          authority: 'authorityManage'
        },
        children: [
          {
            path: 'roleManage',
            name: 'roleManage',
            component: () =>
              import('@views/authorityManage/roleManage/roleManage.vue'),
            meta: {
              title: '角色管理',
              icon: 'role',
              authority: 'roleManage',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'addRole',
                  name: '新增角色'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                }
              ]
            }
          },
          {
            path: 'userManage',
            name: 'userManage',
            meta: {
              title: '用户管理',
              icon: 'user',
              authority: 'userManage',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'add',
                  name: '添加用户'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                },
                {
                  code: 'sync',
                  name: '手工同步'
                },
                {
                  code: 'grant',
                  name: '授权'
                },
                {
                  code: 'copy',
                  name: '复制权限'
                },
                {
                  code: 'reset',
                  name: '重置密码'
                }
              ]
            },
            component: () =>
              import('@views/authorityManage/userManage/userManage.vue')
          },
          {
            path: 'userInfoDetail',
            name: 'userInfoDetail',
            meta: {
              hidden: true,
              title: '用户详情',
              authority: 'userManage',
              parentInfo: {
                meta: {
                  title: '用户管理',
                  clickable: true
                },
                path: 'userManage'
              }
            },
            component: () =>
              import('@views/authorityManage/userManage/userInfoDetail.vue')
          },
          {
            path: 'postManagement',
            name: 'postManagement',
            meta: {
              title: '岗位管理',
              icon: 'station',
              authority: 'postManagement',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'add',
                  name: '添加'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                },
                {
                  code: 'sync',
                  name: '手工同步'
                }
              ]
            },
            component: () =>
              import('@views/authorityManage/postManagement/postManagement.vue')
          },
          {
            path: 'postDetail',
            name: 'postDetail',
            meta: {
              title: '岗位详情',
              authority: 'postManagement',
              hidden: true,
              parentInfo: {
                meta: {
                  title: '岗位管理',
                  clickable: true
                },
                path: 'postManagement'
              }
            },
            component: () =>
              lazyLoadView(
                import('@views/authorityManage/postManagement/postDetail.vue')
              )
          },
          {
            path: 'menuManage',
            name: 'menuManage',
            meta: {
              title: '菜单管理',
              icon: 'menu',
              authority: 'menuManage',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'add',
                  name: '添加'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                }
              ]
            },
            component: () =>
              import('@views/authorityManage/menuManage/menuManage.vue')
          },
          {
            path: 'subscriptionManage',
            name: 'subscriptionManage',
            meta: {
              title: '订阅管理',
              icon: 'menu',
              authority: 'subscriptionManage',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'add',
                  name: '添加'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                }
              ]
            },
            component: () =>
              import(
                '@views/authorityManage/subscriptionManage/subscriptionManage.vue'
              )
          }
        ]
      },
      {
        path: 'systemSetting',
        name: 'systemSetting',
        component: AuthorityManage,
        meta: {
          title: '系统设置',
          icon: 'auth',
          authority: 'systemSetting'
        },
        children: [
          {
            path: 'applicationManage',
            name: 'applicationManage',
            meta: {
              title: '应用管理',
              icon: 'auth',
              authority: 'applicationManage',
              actions: [
                {
                  code: 'query',
                  name: '查询'
                },
                {
                  code: 'addRole',
                  name: '新增'
                },
                {
                  code: 'edit',
                  name: '修改'
                },
                {
                  code: 'del',
                  name: '删除'
                }
              ]
            },
            component: () =>
              import(
                '@views/authorityManage/systemSetting/applicationManage/applicationManage.vue'
              )
          },
          {
            path: 'applicationAdd',
            name: 'applicationAdd',
            meta: {
              title: '新增',
              authority: 'applicationManage',
              hidden: true,
              parentInfo: {
                meta: {
                  title: '应用管理',
                  clickable: true
                },
                path: 'applicationManage'
              }
            },
            component: () =>
              lazyLoadView(
                import(
                  '@views/authorityManage/systemSetting/applicationManage/applicationAdd.vue'
                )
              )
          }
        ]
      }
    ]
  }
];
