import Layout from '@layouts/main';

export default [
  {
    path: '/sysSupport',
    component: Layout,
    //   添加hidden属性，在菜单中隐藏该模块，注释hidden即可显示
    // hidden:true,
    meta: {
      title: '系统支持',
      authority: 'sysSupport',
      icon: 'support'
    },
    order: 3,
    redirect: '/sysSupport/',
    children: [
      {
        path: 'dataService',
        name: 'dataService',
        meta: {
          title: '数据管理',
          icon: 'database',
          authority: 'dataService'
        },
        component: () => import('@components/MiddleComponent'),
        children: [
          {
            path: 'viewDateManagent',
            name: 'viewDateManagent',
            meta: {
              title: '数据视图管理',
              icon: 'dataview',
              authority: 'viewDateManagent'
            },
            component: () => import('@views/viewDateManagent/index.vue')
          },
          {
            path: 'pushService',
            name: 'pushService',
            meta: {
              title: '数据推送服务',
              icon: 'push',
              authority: 'pushService'
            },
            component: () => import('@views/pushService/index.vue')
          },
          {
            path: 'httpPushService',
            name: 'httpPushService',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '数据推送服务编辑',
              authority: 'pushService',
              parentInfo: {
                meta: {
                  title: '数据推送服务',
                  clickable: true
                },
                path: 'pushService'
              }
            },
            component: () => import('@views/pushService/httpPushService.vue')
          },
          {
            path: 'dataBaseView',
            name: 'dataBaseView',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '数据库类型视图',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/dataBaseView.vue')
          },
          {
            path: 'httpView',
            name: 'httpView',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: 'http服务类型视图',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/httpView.vue')
          },
          {
            path: 'detailData',
            name: 'detailData',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '视图详情',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/detailData.vue')
          },
          {
            path: 'fileView',
            name: 'fileView',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '文件类型视图',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/fileView.vue')
          },
          {
            path: 'customView',
            name: 'customView',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '自定义数据视图',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/customView.vue')
          },
          {
            path: 'connector',
            name: 'connector',
            meta: {
              hidden: true,
              ignoreAuth: true,
              title: '连接器管理',
              authority: 'viewDateManagent',
              parentInfo: {
                meta: {
                  title: '数据视图管理',
                  clickable: true
                },
                path: 'viewDateManagent'
              }
            },
            component: () => import('@views/viewDateManagent/connector.vue')
          }
        ]
      },
      {
        path: 'pageTemplate',
        name: 'pageTemplate',
        meta: {
          title: '页面模板管理',
          icon: 'template',
          authority: 'pageTemplate'
        },
        component: () => import('@views/pageTemplate')
      },
      {
        path: 'editPageTemplate',
        name: 'editPageTemplate',
        meta: {
          hidden: true,
          title: '页面模板编辑器',
          authority: 'pageTemplate',
          parentInfo: {
            meta: {
              title: '页面模板管理',
              clickable: true
            },
            path: 'pageTemplate'
          }
        },
        component: () => import('@views/pageTemplate/edit.vue')
      },
      {
        path: 'monitorManagement',
        name: 'monitorManagement',
        meta: {
          title: '监控管理',
          icon: 'template',
          authority: 'monitorManagement'
        },
        component: () => import('@views/monitorManagement/index.vue')
      }
    ]
  }
];
