import Layout from '@layouts/main';

export default [
  {
    path: '/taskConfigCenter',
    component: Layout,
    meta: {
      title: '任务配置中心',
      authority: 'taskConfigCenter',
      icon: 'support'
    },
    order: 7,
    redirect: '/taskConfigCenter/',
    children: [
      {
        path: 'databaseManage',
        name: 'databaseManage',
        component: () =>
          import('@views/taskConfigCenter/operationLibraryManage/index.vue'),
        meta: {
          title: '操作库管理',
          icon: 'dataview',
          authority: 'databaseManage'
        },
        children: [
          {
            path: 'checkItem',
            name: 'checkItem',
            component: () =>
              import(
                '@views/taskConfigCenter/operationLibraryManage/checkItem/index.vue'
              ),
            meta: {
              title: '检查项管理',
              icon: 'role',
              authority: 'checkItem'
            }
          },
          {
            path: 'RPAManage',
            name: 'RPAManage',
            component: () =>
              import(
                '@views/taskConfigCenter/operationLibraryManage/RPAManage/index.vue'
              ),
            meta: {
              title: 'RPA管理',
              icon: 'role',
              authority: 'RPAManage'
            }
          },
          {
            path: 'OCRManage',
            name: 'OCRManage',
            component: () =>
              import(
                '@views/taskConfigCenter/operationLibraryManage/OCRManage/index.vue'
              ),
            meta: {
              title: 'OCR管理',
              icon: 'role',
              authority: 'OCRManage'
            }
          }
        ]
      },
      {
        path: 'businessScenarioManage',
        name: 'businessScenarioManage',
        meta: {
          title: '业务场景管理',
          icon: 'dataview',
          authority: 'businessScenarioManage'
        },
        component: () =>
          import('@views/taskConfigCenter/businessScenarioManage/index.vue')
      },
      {
        path: 'orchestrationTask',
        name: 'orchestrationTask',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '编排任务',
          icon: 'dataview',
          authority: 'orchestrationTask',
          parentInfo: {
            meta: {
              title: '业务场景管理',
              clickable: true
            },
            path: 'businessScenarioManage'
          }
        },
        component: () =>
          import(
            '@views/taskConfigCenter/businessScenarioManage/orchestrationTask.vue'
          )
      },
      {
        path: 'taskLibraryManage',
        name: 'taskLibraryManage',
        meta: {
          title: '任务库管理',
          icon: 'dataview',
          authority: 'taskLibraryManage',
          hidden: true,
          ignoreAuth: true,
          parentInfo: {
            meta: {
              title: '业务场景管理',
              clickable: true
            },
            path: 'businessScenarioManage'
          }
        },
        component: () =>
          import('@views/taskConfigCenter/taskLibraryManage/index.vue')
      },
      {
        path: 'addTasks',
        name: 'addTasks',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '任务创建',
          icon: 'dataview',
          authority: 'addTasks',
          parentInfo: {
            meta: {
              title: '任务库管理',
              clickable: true
            },
            path: 'taskLibraryManage'
          }
        },
        component: () =>
          import('@views/taskConfigCenter/taskLibraryManage/addTasks.vue')
      },
      {
        path: 'taskDetail',
        name: 'taskDetail',
        meta: {
          hidden: true,
          ignoreAuth: true,
          title: '任务详情',
          icon: 'dataview',
          authority: 'taskDetail',
          parentInfo: {
            meta: {
              title: '任务库管理',
              clickable: true
            },
            path: 'taskLibraryManage'
          }
        },
        component: () =>
          import('@views/taskConfigCenter/taskLibraryManage/taskDetail.vue')
      },
      {
        path: 'roaster',
        name: 'roaster',
        meta: {
          title: '排班表',
          icon: 'dataview',
          authority: 'roaster'
        },
        component: () => import('@views/taskConfigCenter/roaster/index.vue')
      },
      {
        path: 'dateSetting',
        name: 'dateSetting',
        meta: {
          title: '节假日设置',
          icon: 'dataview',
          authority: 'dateSetting'
        },
        component: () => import('@views/taskConfigCenter/dateSetting/index.vue')
      }
    ]
  }
];
