<template>
  <div v-show="spread" :class="['spaeadStyle', className]">
    <item-render
      :type="editer.type"
      v-bind="$attrs"
      :config="{
        placeholder: editer.placeholder || '',
        ...editer.config,
        model,
        key: editer.key
      }"
      @doInput="handleInput"
    ></item-render>
  </div>
</template>

<script>
import ItemEdit from '@components/renders/itemEdit';
export default {
  name: 'TableColumnEdit',
  components: { 'item-render': ItemEdit },
  props: {
    spread: {
      type: Boolean
    },
    save: {
      type: Boolean
    },
    model: {
      type: Object,
      default: () => []
    },
    // record: {
    //   type: Object,
    //   default: () => {}
    // },
    editer: {
      type: Object,
      default: () => {}
    },
    labelWidth: {
      type: String,
      default: () => ''
    },
    className: {
      type: String,
      default: () => ''
    }
  },
  methods: {
    editchange() {
      this.$emit('editchange');
    },
    handleInput(val) {
      this.$emit('inputChange', val);
    }
  }
};
</script>

<style lang="scss">
.spaeadStyle {
  .el-input--small .el-input__inner {
    height: 27px;
    line-height: 27px;
  }
  .el-form-item--small .el-form-item__content {
    line-height: 27px;
  }
  .el-input--small .el-input__icon {
    line-height: 27px;
  }
  .el-form-item--small .el-form-item__content {
    line-height: 27px;
  }
  .el-input .el-input__inner {
    border-color: rgba(74, 74, 74, 0.2);
    border-radius: 2px;
  }
  .saveStyle {
    .el-input {
      position: relative;
      display: inline-block;
      width: 71%;
      margin-right: 10px;
      font-size: 14px;
    }
    .el-select {
      position: relative;
      display: inline-block;
      width: 79%;
      .el-input {
        position: relative;
        display: inline-block;
        width: 90% !important;
        margin-right: 10px;
        font-size: 14px;
      }
    }
  }
  .el-form-item {
    margin: 2px 0;
  }
}
</style>
