<template>
  <el-select
    v-model="selected"
    :multiple="multiple"
    :filterable="filterable"
    :clearable="clearable"
    :collapse-tags="collapseTags"
    :placeholder="placeholder"
    :filter-method="doFilter"
    @change="handleSelectChange"
  >
    <el-option
      key="treeSelectPanel"
      label=""
      value="treeSelectPanel"
      style="min-height: 240px; min-width: 160px; overflow-y: auto; padding: 0; background-color: #fff;"
    >
      <el-tree
        ref="selectTree"
        :data="datas"
        :show-checkbox="multiple"
        :highlight-current="!multiple"
        :node-key="nodeKey"
        :props="treeProps"
        :default-checked-keys="value"
        :filter-node-method="filterNode"
        @check-change="handleCheck"
      ></el-tree>
    </el-option>
    <el-option
      v-for="node in hiddenOptions"
      :key="node.id"
      :label="node.label"
      :value="node.id"
      class="hidden-options"
    ></el-option>
  </el-select>
</template>

<script>
export default {
  name: 'tree-select',
  props: {
    value: {
      required: true
    },
    multiple: {
      type: Boolean,
      default: false
    },
    treeData: Array,
    treeProps: Object,
    placeholder: {
      type: String,
      default: ''
    },
    root: {
      type: Boolean,
      default: false
    },
    filterable: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: false
    },
    collapseTags: {
      type: Boolean,
      default: false
    },
    nodeKey: {
      type: String,
      default: 'id'
    }
  },
  data() {
    return {
      selected: [],
      hiddenOptions: [],
      timer: null
    };
  },
  watch: {
    value: function() {
      this.initSelected();
    }
  },
  computed: {
    datas() {
      if (this.root) {
        const rootnode = { id: 'tree-select-root' };
        if (this.treeProps) {
          rootnode[this.treeProps['label']] = '全部';
          rootnode[this.treeProps['children']] = this.treeData;
        } else {
          rootnode.label = '全部';
          rootnode.children = this.treeData;
        }
        return [rootnode];
      } else {
        return this.treeData;
      }
    }
  },
  created() {
    this.initSelected();
  },
  methods: {
    handleCheck() {
      this.hiddenOptions = this.$refs.selectTree.getCheckedNodes(true);
      this.selected = this.$refs.selectTree.getCheckedKeys(true);
      this.$emit('input', this.selected);
    },
    handleSelectChange(values) {
      this.$refs.selectTree.setCheckedKeys(values);
    },
    initSelected() {
      if (this.multiple) {
        if (this.selected.length === 0 && this.value.length !== 0) {
          this.selected = this.value;
        }
      } else {
        if (this.value) {
          this.selected = this.value;
        }
      }
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    doFilter(filterText) {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        console.log(filterText);
        this.$refs.selectTree.filter(filterText);
      }, 500);
    }
  }
};
</script>

<style lang="scss" scoped>
.hidden-options {
  display: none;
}

/*滚动条整体部分,必须要设置*/
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #fdfdfd;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track {
  background-color: #fdfdfd;
}
/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dde3ea;
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button {
  height: 10px;
  background-color: #fdfdfd;
}
</style>
