import { arrayFind } from './util';
import { getMenu } from './permission';

const users = [
  {
    id: 1,
    username: 'admin',
    password: 'admin',
    name: 'Vue Master'
  },
  {
    id: 2,
    username: 'user1',
    password: 'password',
    name: 'User One'
  },
  {
    id: 3,
    username: 'lijunfeng',
    password: '123456',
    name: 'User One'
  }
];

export default {
  'post|/auth-center/jwt/login': (options) => {
    let accessToken = '';
    let status = 401;
    const user = JSON.parse(options.body);
    for (let i = 0; i < users.length; i++) {
      if (
        users[i]['username'] === user['username'] &&
        users[i]['password'] === user['password']
      ) {
        accessToken = 'longtimenoseeIamdyingtoseeyou';
        status = 200;
        break;
      }
    }
    return {
      status: status,
      message: 'success',
      data: {
        state: 20101,
        accessToken,
        menuTree: getMenu(),
        userInfo: {
          code: 'admin',
          name: '管理员'
        }
      }
    };
  },
  'get|/auth-center/jwt/authVerify': (options) => {
    const params = 'longtimenoseeIamdyingtoseeyou-1';
    const userId = parseInt(params.split('-')[1]);
    const user = arrayFind(users, 'id', userId);
    user.name = user.username;
    return {
      status: user ? 200 : 404,
      message: 'success',
      data: user
    };
  },
  'get|/auth-center/jwt/logout': () => {
    return {
      status: 200,
      message: 'success',
      data: 'ok'
    };
  },
  'get|/auth-center/jwt/refresh': () => {
    return {
      status: 200,
      message: 'success',
      data: 'ok'
    };
  }
};
