import axios from '../utils/ajax';

const serverUrl = axios.serverUrl;
// const serverUrl = axios.serverUrlMock;
const serverName = '/operation-base-table';

// 1.1、查询自定义列信息
export async function queryCustom(params) {
  return axios.get(
    serverUrl(`${serverName}/custom-column/search-column-list`),
    params
  );
}

// 1.2、保存自定义列信息
export async function saveCustom(obj) {
  return axios.post(
    serverUrl(`${serverName}/custom-column/save-column-list`),
    obj
  );
}
