








import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import config from './config';
import echarts from 'echarts';
import 'echarts-liquidfill';

@Component
export default class BaseChart extends Vue {
  private chart: any = null;

  @Prop({ required: true }) type: string; // 图表类型
  @Prop({ default: {} }) options: any; // 自定义配置
  @Prop({ default: '100%' }) width: string;
  @Prop({ default: '100%' }) height: string;

  get defaultOptions() {
    return config[this.type];
  }

  @Watch('options')
  optionsChange() {
    this.resetChart();
    this.initChart();
  }

  // 根据配置进行初始化
  initChart() {
    this.chart = echarts.init(this.$el as any);
    this.chart.setOption(this.defaultOptions);
    this.chart.setOption(this.options);
    this.registerEvent();
  }

  // 销毁实例
  resetChart() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  }

  resizeChart() {
    window.setTimeout(() => {
      this.chart.resize();
    }, 100);
  }

  // 注册事件
  registerEvent() {
    // 自定义点击事件回调
    config.ACTION_EVENTS.forEach((event) => {
      this.chart.on(event, (params) => {
        this.$emit(event, params);
      });
    });

    // 自定义鼠标事件回调
    config.MOUSE_EVENTS.forEach((event) => {
      this.chart.on(event, (params) => {
        this.$emit(event, params);
      });
    });

    window.addEventListener('resize', this.resizeChart, false);
  }

  mounted() {
    this.initChart();
  }

  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    window.removeEventListener('resize', this.resizeChart, false);
    this.chart.dispose();
  }
}
