import RoutesMapConfig from '../router/routes';

function switchRouterToMenu(parent, routers) {
  const menus = [];
  routers.forEach((item) => {
    if (
      item.path.indexOf('home') > -1 ||
      item.path.indexOf('dynamicPage') > -1
    ) {
      return;
    }
    const menu = {};
    menu.path = item.path;
    if (item.meta) {
      const meta = item.meta;
      if (meta.hidden) {
        return;
      }
      menu.code = meta.authority;
      menu.title = meta.title;
      menu.icon = meta.icon ? meta.icon : '';
      menu.id = meta.authority;
      menu.type = 0;
      menu.order = item.order;
    }

    if (item.children && item.children.length) {
      menu.children = switchRouterToMenu(menu.code, item.children);
    }
    menus.push(menu);
  });
  return menus;
}
//
const menus = switchRouterToMenu('', RoutesMapConfig).sort(function(a, b) {
  return (a.order || 1) - (b.order || (a.order || 1) + 1);
});

export function getMenu() {
  return menus;
}

export default {
  'post|/auth-center/base/resource/list': (options) => {
    return {
      status: 200,
      message: 'success',
      data: menus
    };
  }
};
