import Vue from 'vue';
import VueRouter from 'vue-router';
import VueMeta from 'vue-meta';

import store from '@store';
import NProgress from 'nprogress';
import { arrayFind, arrayFindDeepWithType } from '@utils/util';
import { getSessionStorage } from '@utils/storage';

import RoutesMapConfig from './routes';
import commonRoutesMap from './commonRoutes';

NProgress.configure({
  showSpinner: false
});

Vue.use(VueRouter);
Vue.use(VueMeta, {
  // The component option name that vue-meta looks for meta info on.
  keyName: 'page'
});

function checkMenuAuth(route: any) {
  if (route.meta.ignoreAuth) {
    return true;
  }
  const r = route;
  let currCode = r.meta.authority;
  const index = r.path.lastIndexOf('/');
  if (index > 0) {
    const parentRouter = arrayFind(r.matched, 'path', r.path.slice(0, index));
    if (parentRouter) {
      currCode = `${parentRouter.meta.authority}_${currCode}`;
    }
  }

  // 通过页面模板新增页面单独处理
  if (r.path.indexOf('dynamicPage') > -1) {
    currCode = 'dynamicPage';
  }

  if (arrayFindDeepWithType(store.getters.authorizes, 'authorize', currCode)) {
    return true;
  } else {
    return false;
  }
}

const routerInstance = new VueRouter({
  routes: commonRoutesMap.concat(RoutesMapConfig),
  mode: 'history',
  base: ''
});

// 不重定向白名单
const whiteList = ['/login'];

routerInstance.beforeEach((to, from, next: any) => {
  NProgress.start();
  if (getSessionStorage('auth_token')) {
    if (!store.getters.authorizes) {
      //  加载用户权限
      store
        .dispatch('fetchAuthorizes')
        .then(() => {
          next({ ...to });
        })
        .catch();
    } else {
      if (!checkMenuAuth(to)) {
        store.dispatch('logOut');
        next({
          path: '/login'
        });
        next();
      } else {
        next();
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next();
    } else {
      next('/login');
      NProgress.done();
    }
  }
});

routerInstance.afterEach(() => {
  NProgress.done();
  const elMain = document.getElementsByClassName('el-main')[0];
  elMain && elMain.scrollTo(0, 0);
});

export default routerInstance;
