<template>
  <div class="navMenu">
    <template v-for="navMenu in navMenus">
      <!-- 最后一级菜单 -->
      <el-menu-item
        v-if="!navMenu.children || !navMenu.children.length"
        :key="navMenu.id"
        :index="navMenu.routerCode"
      >
        <svg-icon
          v-if="navMenu.icon && navMenu.icon !== ' '"
          :icon-class="navMenu.icon"
          class="navMenuIcon"
        ></svg-icon>

        <span slot="title">{{ navMenu.title }}</span>
      </el-menu-item>

      <!-- 此菜单下还有子菜单 -->
      <el-submenu
        v-if="navMenu.children && navMenu.children.length > 0"
        :key="navMenu.id"
        :index="navMenu.routerCode"
      >
        <template slot="title">
          <svg-icon
            v-if="navMenu.icon && navMenu.icon !== ' '"
            :icon-class="navMenu.icon"
            class="navMenuIcon"
          ></svg-icon>
          <span> {{ navMenu.title }}</span>
        </template>
        <!-- 递归 -->
        <NavMenu :nav-menus="navMenu.children"></NavMenu>
      </el-submenu>
    </template>
  </div>
</template>

<script>
export default {
  name: 'NavMenu',
  props: { navMenus: { type: Array, required: true } }
};
</script>
<style lang="scss">
.navMenu .navMenuIcon {
  width: 14px;
  height: 14px;
  margin-right: 5px;
}
</style>
<style lang="scss">
.navMenu {
  background-color: $color-back-click;
  transition: background-color 0.8s;
  .el-submenu .el-menu-item {
    font-size: 16px;
    color: #ffffff;
    line-height: 22px;
    height: 40px;
    padding: 9px 45px;
    min-width: 200px;
    background-color: $color-back-click !important;
    transition: background-color 0.8s;
  }
  .el-menu-item {
    // margin-left: 20px !important;
    color: $color-menu-default-text !important;
    // border-radius: 30px 0 0 30px;
    &:focus,
    &:hover,
    &.is-active {
      // font-weight: bold;
      color: $color-menu-check-text !important;
      transition: background-color 0.3s;
      background: $color-main !important;
    }
  }
  .el-submenu {
    .el-submenu__title {
      background: $color-back-nav;
      transition: background-color 0.8s;
      padding-left: 26px;
      font-size: 16px;
      color: $color-menu-default-text;
      &:focus,
      &:hover {
        padding-left: 20px;
        color: #fff;
      }
    }
    &.is-active {
      .el-submenu__title {
        padding-left: 20px;
      }
    }
    .el-menu--horizontal {
      .el-menu-item {
        color: $color-white;
        &:focus,
        &:hover,
        &.is-active {
          color: $color-main-hover;
          background: $color-main !important;
        }
      }
    }
  }
  .el-menu--inline {
    .el-submenu__title {
      background: $color-back-click;
      transition: background-color 0.8s;
    }
  }
}
</style>
