import ajax from '@utils/ajax';
const serverPre = ajax.serverUrl;
// const serverPre = ajax.serverUrlMock;
const serverName = '/auth-center';
const msgServerName = '/amc-message-center/mail_info';

export async function handleLogin(data) {
  return ajax.post(serverPre(`${serverName}/jwt/login`), data);
}

export async function fetchPermissions(params) {
  return ajax.get(serverPre(`${serverName}/user/get-permission`), params);
}

export async function logout() {
  return ajax.get(serverPre(`${serverName}/jwt/logout`));
}

export async function editPassword(data) {
  return ajax.post(serverPre(`/auth-user-center/user/change-password`), data);
}

// 获取未读消息列表
export async function mailQuery(data) {
  return ajax.post(serverPre(`${msgServerName}/queryMail`), data);
}

// 单条标记已读
export async function updateOneRead(messageId) {
  return ajax.get(serverPre(`${msgServerName}/updateAlreadyRead/${messageId}`));
}

// 全部标记已读
export async function updateAllRead() {
  return ajax.get(serverPre(`${msgServerName}/updateAllRead`));
}

// 批量标记已读
export async function updateSomeRead(params) {
  return ajax.post(serverPre(`${msgServerName}/updateBatchRead`), params);
}

// 获取当前用户岗位
export async function queryUserStation() {
  return ajax.get(serverPre(`/auth-user-center/user/getStationByUserCode`));
}
