

































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {}
})
export default class Theme extends Vue {
  theme = '';
  themeCge(val) {
    let themeProgram = {};
    if (val == 'default') {
      themeProgram = {
        '--color-back-nav': '#091022',
        '--color-back-click': '#030A1C',
        '--color-main': 'rgba(47, 106, 255, 1)',
        '--color-main-opcity': 'rgba(47, 106, 255, 0.1)',
        '--color-btn-hover': '#618EFF',
        '--color-Disable': '#ACC3FF',
        '--color-btn-click': '#2554CC',
        '--color-Success': 'rgba(111,180,35,1)',
        '--color-Warning': 'rgba(245,166,35,1)',
        '--color-Error': 'rgba(223,81,70,1)',
        '--color-Silent': '#95A4BC',
        '--color-back-page': '#F5F7FC',
        '--color-back-header': '#F5F7FC',
        '--color-back-card': '#fff',
        '--color-form-hover': '#F8F8F8',
        '--color-control': '#E1E4EB',
        '--color-table-cutting': '#EBECF0',
        '--color-icon': '#BFC3CC',
        '--color-subtitle-text': '#242633',
        '--color-main-text': '#474C5C',
        '--color-link-text': '#2f6aff',
        '--color-Success-hover': 'rgba(111, 180, 35, .75)',
        '--color-Success-disable': 'rgba(111, 180, 35, .4)',
        '--color-Success-click': '#58901c',
        '--color-Warning-hover': 'rgba(245, 166, 35, .75)',
        '--color-Warning-disable': 'rgba(245, 166, 35, .4)',
        '--color-Warning-click': '#c4841c',
        '--color-Error-hover': 'rgba(223, 81, 70, .75)',
        '--color-Error-disable': 'rgba(223, 81, 70, .4)',
        '--color-Error-click': '#b24038'
      };
    } else if (val == '#5D9FCB') {
      themeProgram = {
        '--color-back-nav': '#39464F',
        '--color-back-click': '#303D46',
        '--color-main': '#5D9FCB',
        '--color-main-opcity': 'rgba(93, 159, 203, 0.1)',
        '--color-btn-hover': '#7EB2D5',
        '--color-Disable': '#C5DCEC',
        '--color-btn-click': '#538FB7',
        '--color-Success': '#71CDA2',
        '--color-Warning': 'rgba(248, 176, 58, 1)',
        '--color-Error': 'rgba(241, 133, 133, 1)',
        '--color-Silent': '#95A4BC',
        '--color-back-page': '#F2F4F8',
        '--color-back-header': '#FAFBFC',
        '--color-back-card': '#fff',
        '--color-form-hover': '#EFF5F9',
        '--color-control': '#D3DBE0',
        '--color-table-cutting': '#F1F4F8',
        '--color-icon': '#BFC3CC',
        '--color-subtitle-text': '#21292F',
        '--color-main-text': '#677B8A',
        '--color-link-text': '#5d9fcb',
        '--color-Success-hover': 'rgba(113, 205, 162, .75)',
        '--color-Success-disable': 'rgba(113, 205, 162, .4)',
        // '--color-Success-click': '#',
        '--color-Warning-hover': 'rgba(248, 176, 58,.75)',
        '--color-Warning-disable': 'rgba(248, 176, 58,.4)',
        // '--color-Warning-click': '#',
        '--color-Error-hover': 'rgba(241, 133, 133, .75)',
        '--color-Error-disable': 'rgba(241, 133, 133, .4)'
        // '--color-Error-click': '#'
      };
    } else if (val == 'black') {
      themeProgram = {
        '--color-back-nav': '#20201F',
        '--color-back-click': '#2C2C2D',
        '--color-main': 'rgba(47,106,255,1)',
        '--color-main-opcity': 'rgba(47,106,255,0.1)',
        '--color-btn-hover': '#618EFF',
        '--color-Disable': '#ACC3FF',
        '--color-btn-click': '#2554CC',
        '--color-Success': 'rgba(111,180,35,1)',
        '--color-Warning': 'rgba(245,166,35,1)',
        '--color-Error': 'rgba(223,81,70,1)',
        '--color-Silent': 'rgba(149,164,188,1)',
        '--color-back-page': '#151413',
        '--color-back-header': '#252525',
        '--color-back-card': '#20201F',
        '--color-form-hover': 'rgba(255,255,255,0.1)',
        '--color-control': '#4b4b4b',
        '--color-table-cutting': '#979797',
        '--color-icon': '#999999',
        '--color-subtitle-text': '#d9d9d9',
        '--color-main-text': '#b3b3b3',
        '--color-link-text': '#2f6aff',
        '--color-Success-hover': 'rgba(111,180,35,0.75)',
        '--color-Success-disable': 'rgba(111,180,35,0.4)',
        '--color-Success-click': '#58901c',
        '--color-Warning-hover': 'rgba(245,166,35,0.75)',
        '--color-Warning-disable': 'rgba(245,166,35,0.4)',
        '--color-Warning-click': '#c4841c',
        '--color-Error-hover': 'rgba(223,81,70,0.75)',
        '--color-Error-disable': 'rgba(223,81,70,0.4)',
        '--color-Error-click': '#b24038'
      };
    } else if (val == 'yellow') {
      themeProgram = {
        '--color-back-nav': '#9a9c02',
        '--color-back-click': '#cccd6f',
        '--color-main': '#f8fa36'
      };
    } else if (val == 'green') {
      themeProgram = {
        '--color-back-nav': '#0b5001',
        '--color-back-click': '#76bc6a',
        '--color-main': '#7cfe67'
      };
    } else if (val == 'blue') {
      themeProgram = {
        '--color-back-nav': '#2554f0',
        '--color-back-click': '#93aaf5',
        '--color-main': '#0c38c5'
      };
    }
    this.setTheme(themeProgram);
    localStorage.setItem('theme', val);
  }
  setTheme(val) {
    for (const key in val) {
      window.document.documentElement.style.setProperty(key, val[key]);
    }
  }
  mounted() {
    const theme = localStorage.getItem('theme');
    if (theme) {
      this.themeCge(theme);
      this.theme = theme;
    }
  }
}
