<template>
  <div class="navmenu_out">
    <el-menu
      :active-text-color="activeTextColor"
      :default-active="$route.path"
      :collapse="foldingState"
      :unique-opened="true"
      mode="vertical"
      router
    >
      <NavMenu :nav-menus="menuData"></NavMenu>
    </el-menu>
  </div>
</template>
<script>
import { mapState } from 'vuex';
import NavMenu from './NavMenu.vue';
export default {
  components: {
    NavMenu: NavMenu
  },
  data() {
    return {
      activeIndex: 'home',
      activeTextColor: '#313236'
    };
  },
  computed: {
    ...mapState({
      foldingState: (state) => state.collapse.foldingState
    }),
    menuData() {
      // console.log(this.$store.getters.authorizes);
      return this.$store.getters.authorizes;
    }
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
* {
  outline: none;
}
</style>

<style lang="scss">
.el-menu--collapse {
  .el-submenu__title > span,
  .el-submenu__title > i {
    display: none;
  }
}
.el-menu.v-leave-to {
  .el-submenu__title > span,
  .el-submenu__title > i,
  .is-opened {
    display: none;
  }
}
.el-menu {
  border: none;
  background-color: $color-back-header;
}
.navmenu_out .el-menu {
  background-color: $color-back-click;
}
.is-opened {
  .is-active {
    background-color: #1c3b82;
  }
  overflow: hidden;
}

.el-menu.el-menu--horizontal {
  background-color: $color-main;
  border-bottom: none;
  .el-menu-item {
    float: left;
    height: 46px;
    padding: 0 15px;
    line-height: 46px;
    color: $color-white;
    &:focus,
    &:hover,
    &.is-active {
      color: $color-white !important;
      background: $color-table-header-bg !important;
    }
  }
  .el-submenu {
    float: left;
    .el-submenu__title {
      height: 46px;
      padding: 0 15px;
      line-height: 46px;
      color: $color-white;
      > i {
        display: none;
      }
      &:focus,
      &:hover {
        // font-weight: bold;
        color: $color-white;
        background: $color-table-header-bg !important;
        // border-bottom: 3px solid $color-main-hover !important;
      }
    }
    &.is-active {
      .el-submenu__title {
        // font-weight: bold;
        color: $color-white !important;
        background: $color-table-header-bg !important;
        // border-bottom: 3px solid $color-main-hover !important;
      }
    }
  }
}
.el-menu.el-menu--horizontal:link {
  // border-color: red;
  // box-shadow: none;
  background-color: red;
}
</style>
