<template>
  <div class="tags-view-container">
    <router-link
      v-for="tag in visitedViews"
      :key="tag.path"
      :to="tag.fullPath"
      draggable="false"
      class="tags-view-item"
    >
      <span class="title">{{ tag.customTitle || tag.title }}</span>
      <span
        v-show="visitedViews.length > 1 && tag.title !== '首页'"
        class="icon el-icon-close1"
        @click.prevent.stop="closeSelectedTag(tag)"
      >
        <i class="el-icon-close"></i>
      </span>
    </router-link>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
// import ScrollWrap from './ScrollWrap';
export default {
  name: 'TagViews',
  components: {
    // ScrollWrap
  },
  data() {
    return {};
  },
  computed: {
    ...mapState({
      visitedViews: (state) => state.tagsView.visitedViews
    })
  },
  watch: {
    $route() {
      this.addViewTags();
    }
  },
  mounted() {
    this.addViewTags();
  },
  methods: {
    ...mapActions('tagsView', ['addView', 'delView']),
    generateRoute() {
      if (this.$route.name && this.$route.name !== '404') {
        return this.$route;
      }
      return false;
    },
    isActive(route) {
      return route.path === this.$route.path;
    },
    addViewTags() {
      const route = this.generateRoute();
      if (!route) {
        return false;
      }
      this.addView(route);
    },
    closeSelectedTag(view) {
      this.delView(view).then(({ visitedViews }) => {
        if (this.isActive(view)) {
          const latestView = visitedViews.slice(-1)[0];
          if (latestView) {
            // 如果关闭标签页后需要显示的是自定义页面，需要将路由的name属性去掉，按照path跳转，否则会出现跳转路径错误的问题
            if (latestView.name === 'dynamicPage') {
              delete latestView.name;
            }

            this.$router.push(latestView);
          } else {
            this.$router.push('/');
          }
        }
      });
    },
    handleMousedown(e) {
      // @mousedown.native="handleMousedown"
      const target = e.currentTarget;
      const disX = e.clientX - target.offsetLeft;
      document.onmousemove = (e) => {
        const left = e.clientX - disX;
        if (left < 0) return;
        target.style.left = left + 'px';
      };
      document.onmouseup = function(e) {
        document.onmousemove = null;
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.tags-view-container {
  background-color: $color-back-card;
  transition: background-color 0.8s;
  // box-shadow: inset 0 -1px 0 0 $color-line-nav;
  .tags-view-item {
    position: relative;
    display: inline-block;
    height: 30px;
    padding: 0 10px;
    margin-left: 10px;
    font-size: 12px;
    line-height: 30px;
    color: $color-main-text;
    // background: $color-main;
    // border: 1px solid $color-body-bg;
    border-bottom: none;
    border-radius: 2px 2px 0 0;
    &:first-of-type {
      margin-left: 20px;
    }
    &:last-of-type {
      margin-right: 20px;
    }
    &.router-link-exact-active {
      color: $color-subtitle-text;
      font-weight: bold;
      background-color: $color-back-page;
      border-color: $color-back-page;
      transition: background-color 0.8s;
    }
    .icon {
      margin-left: 6px;
      font-size: 15px;
      &:hover {
        background: rgba($color-mainfont, 0.2);
        border-radius: 50%;
      }
    }
  }
  .draging {
    top: 0;
    left: 0;
  }
}
</style>
