export default [
  {
    path: '/login',
    meta: {
      title: '登录',
      ignoreAuth: true
    },
    component: () => import('@views/login/login')
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/404',
    meta: {
      title: 'Page Not Found',
      ignoreAuth: true
    },
    component: () => import('@views/_404')
  }
  // 404 page must be placed at the end !!!
  // { path: '*', redirect: '/404', hidden: true }
];
