<template>
  <div v-show="spread" class="transitionBox">
    <el-form
      ref="adsfrom"
      v-loading="screenLoading"
      class="grid"
      :label-width="labelWidth"
      :model="model"
      :rules="rules"
      size="medium"
    >
      <div class="grid">
        <div v-for="item in items" :key="item.key" class="box">
          <el-form-item :label="item.label" :prop="item.key">
            <item-render
              :type="item.type"
              :config="{
                placeholder: item.placeholder || '',
                ...item.config,
                model,
                key: item.key
              }"
            />
          </el-form-item>
        </div>
        <el-form-item
          :class="[boxBtnStyle, boxBtnStyle === 'boxBtn1' ? 'box' : '']"
        >
          <el-button type="primary" @click="handleQuery">查询</el-button>
          <el-button @click="handleReset('adsfrom')">重置</el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>

<script>
import ItemRender from '@components/renders/ItemRender';
export default {
  name: 'AdvancedScreening',
  components: { 'item-render': ItemRender },
  props: {
    spread: {
      type: Boolean
    },
    model: {
      type: Object,
      default: () => {}
    },
    items: {
      type: Array,
      default: () => []
    },
    labelWidth: {
      type: String,
      default: () => ''
    },
    screenLoading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      rules: {},
      boxBtnStyle: ''
    };
  },
  created() {
    this.initRules();
  },
  mounted() {
    if (this.items.length < 5) {
      this.boxBtnStyle = 'boxBtn1';
    } else if (this.items.length % 5 === 0) {
      this.boxBtnStyle = 'boxBtn3';
    } else {
      this.boxBtnStyle = 'boxBtn2';
    }
  },
  methods: {
    handleQuery() {
      this.$emit('doQuery');
    },
    handleReset(formName) {
      this.$refs[formName].resetFields();
      this.$emit('doReset');
    },
    initRules() {
      const rules = {};
      this.items.forEach((item) => {
        if (item.rule) {
          rules[item.key] = item.rule;
        }
      });
      this.rules = rules;
    }
  }
};
</script>
