import { queryCustom, saveCustom } from '../api/customColumn';
// import CheckboxComponent from '../components/renders/checkboxComponent';
export default {
  data() {
    return {
      dragState: {
        start: -1, // 起始元素的 index
        end: -1, // 移动鼠标时所覆盖的元素 index
        dragging: false, // 是否正在拖动
        direction: undefined // 拖动方向
      },
      tableHeader: [],
      visible: false,
      allColumns: []
    };
  },
  created() {
    this.tableCode && this.queryCustomColumn();
  },
  computed: {
    checkedCol() {
      return this.tableHeader.map((item) => item.id);
    }
  },
  methods: {
    renderHeader(col) {
      if (!this.tableCode) return;
      col.renderHeader = (h, { column, $index }) => {
        // let _self = this;
        // let checkboxComponent;
        // if (column.label === '') {
        //   checkboxComponent = (
        //     <CheckboxComponent
        //       tableCode={_self.tableCode}
        //       // checkedCol={_self.checkedCol}
        //       // allColumns={_self.allColumns}
        //       onResetHeader={_self.resetHeader}
        //     ></CheckboxComponent>
        //   );
        // } else {
        //   checkboxComponent = null;
        // }
        return (
          <div
            class="thead-cell"
            onDragover={(e) => this.handleDragover(e, column, $index)}
            onMouseenter={($event) =>
              this.handleMouseenter($event, column, $index)
            }
            onDrag={($event) => this.handleDrag($event, column, $index)}
            onDragstart={($event) =>
              this.handleDragStart($event, column, $index)
            }
            onDragenter={($event) =>
              this.handleDragenter($event, column, $index)
            }
            onDragend={($event) => this.handleDragend($event, column, $index)}
            onDrop={($event) => this.handleDrop($event, column, $index)}
          >
            <a> {column.label} </a>
            {/* {checkboxComponent} */}
            {/* <CheckboxComponent
                checkedCol={_self.checkedCol}
                allColumns={_self.allColumns}
                onResetHeader={_self.resetHeader}
              ></CheckboxComponent> */}
          </div>
        );
      };
    },
    resetHeader(newHeader) {
      this.tableHeader = newHeader;
      this.$emit('sortColumns', this.tableHeader);
    },
    handleDragover(e, column, $index) {
      if (column.fixed === false) {
        e.preventDefault();
      }
    },
    handleMouseenter(e, column, $index) {
      if (column.fixed && column.label !== '') {
        e.target.style.cursor = 'no-drop';
        return;
      }
      e.target.draggable = true;
      this.dragState.dragging = true;
      this.dragState.start = this.tableHeader.findIndex(
        (item) => column.label === item.label
      );
    },
    handleDrag(e, column, $index) {},
    handleDragStart(e, column, $index) {
      if (column.fixed || column.label === '操作') return;
      e.target.draggable = true;
      this.dragState.dragging = true;
      this.dragState.start = this.tableHeader.findIndex(
        (item) => column.label === item.label
      );
    },
    handleDragenter(e, column, $index) {
      e.preventDefault();
      const startIndex = this.tableHeader.findIndex(
        (item) => column.label === item.label
      );
      if (this.dragState.dragging) {
        const index = startIndex; // 记录起始列
        if (index - this.dragState.start !== 0) {
          this.dragState.direction =
            index - this.dragState.start < 0 ? 'left' : 'right'; // 判断拖动方向
          this.dragState.end = parseInt(startIndex);
        } else {
          this.dragState.direction = undefined;
        }
      } else {
        return false;
      }
    },
    handleDragend($event, column, $index) {},
    handleDrop($event, column, $index) {
      this.dragColumn(this.dragState);
      this.$emit('sortColumns', this.tableHeader);
      // 初始化拖动状态
      this.dragState = {
        start: -1,
        end: -1,
        dragging: false,
        direction: undefined
      };
    },
    // 拖动易位
    dragColumn({ start, end, direction }) {
      // console.log(start, end, direction);
      if (direction === 'left') {
        this.tableHeader.splice(end, 0, this.tableHeader[start]);
        this.tableHeader.splice(start + 1, 1);
      } else if (direction === 'right') {
        this.tableHeader.splice(end + 1, 0, this.tableHeader[start]);
        this.tableHeader.splice(start, 1);
      }
      this.saveCustom();
    },
    queryCustomColumn() {
      const params = {
        tableCode: this.tableCode
      };
      queryCustom(params).then((data) => {
        if (data.error) {
          this.$message.error('自定义信息查询失败：' + data.error.message);
        } else {
          this.tableHeader = data.data.showList;
          this.allColumns = data.data.showList.concat(data.data.hiddenList);
        }
      });
    },
    saveCustom() {
      const showListIds = this.tableHeader.map((item) => item.id);
      const params = {
        showListIds,
        tableCode: this.tableCode
      };
      saveCustom(params).then((data) => {
        if (data.error) {
          return this.$message.error('自定义信息保存失败' + data.error.message);
        } else {
          // this.queryCustomColumn();
        }
      });
    }
  }
};
