
























import TabItem from './TabItem.vue';
import { Component, Prop, Emit, Vue, Watch } from 'vue-property-decorator';

@Component({
  components: {
    TabItem
  }
})
export default class Tabs extends Vue {
  @Prop() tabList: Array<any>;
  @Prop() tabActive: string;
  @Prop() tabPosition: string;

  activeName = '';
  className = '';

  @Watch('tabActive')
  watchTabUpate(newV) {
    this.activeName = newV;
  }

  mounted() {
    this.activeName = this.tabActive;
    /*
         通过tabPosition 定tab切换位置
          标签居右、下   插入name=up位置
          标签居左、上   插入 name=down位置
         */
    switch (this.tabPosition) {
      case 'left':
        this.className = 'tabs-left';
        break;
      case 'right':
        this.className = 'tabs-left';
        break;
      default:
        this.className = '';
        break;
    }
  }
  @Emit('tabClick') send(currentCom: string) {}
  tabClick(tab, event) {
    this.send(tab.name);
  }

  // 流程图右侧属性面板，点击节点时使用
  updateActiveName(activeName) {
    this.activeName = activeName;
  }
}
